@use "../mixins/mini";
@use "../mixins/breakpoint";

.auth {
  margin-bottom: mini.rem(90);
  @include breakpoint.small-tablet {
    margin-bottom: mini.rem(80);
  }
  @include breakpoint.mobile {
    margin-bottom: mini.rem(60);
  }
  // .auth__container
  &__container {
  }
  // .auth__title
  &__title {
    max-width: mini.rem(488);
  }
  &__text {
    margin: 0 auto;
    font-size: mini.rem(18);
    line-height: 167%;
    text-align: center;
    margin-bottom: mini.rem(40);
    max-width: mini.rem(488);
    span {
      display: inline-block;
      font-weight: 600;
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(30);
    }
  }
  // .auth__form
  &__form {
    width: 100%;
    max-width: mini.rem(360);
    margin: 0 auto;
    &:not(:last-child) {
      margin-bottom: mini.rem(33);
    }
  }
  // .auth__nav
  &__nav {
    max-width: mini.rem(360);
    margin: 0 auto;
    text-align: center;
  }
  // .auth__list
  &__list {
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: mini.rem(15);
    }
    &--timeout {
      display: flex;
      flex-direction: column;
      .auth__link {
        display: none;
      }
    }
  }
  // .auth__item
  &__item, &__descr {
    font-size: mini.rem(15);
    line-height: 160%;
    text-align: center;
    color: rgba(0, 0, 0, 0.5);
  }
  // .auth__link
  &__link {
    font-weight: 500;
    color: var(--main-color);
    transition: color 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        color: var(--accent-color);
      }
    }
    &:focus {
      color: var(--accent-color);
    }
  }
  .form__body {
    margin-bottom: mini.rem(40);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(30);
    }
  }
  .form__descriptions {
    margin-bottom: mini.rem(32);
  }
  .form__checkbox {
    justify-content: center;
    margin-bottom: mini.rem(28);
  }
  .checkbox__box {
    &::before {
      background: var(--bg-color);
    }
  }
  .label {
  }
  .input {
    background: var(--bg-color);
  }
  .button, .input {
    width: 100%;
  }
}