@use "../mixins/mini";
@use "../mixins/breakpoint";


.call {

  background-image: url(../img/logo-bg.png);
  background-repeat: no-repeat;
  background-size: mini.rem(1030) mini.rem(520);
  background-position: center bottom;
  padding: mini.rem(75) 0 mini.rem(110);
  // .call__container
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  // .call__title
  &__title {
    margin-bottom: mini.rem(23);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(18);
    }
  }
  // .call__subtitle
  &__subtitle {
    max-width: mini.rem(880);
    margin-bottom: mini.rem(45);
    text-align: center;
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(40);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(34);
    }
  }
  // .call__button
  &__button {
    margin-bottom: mini.rem(45);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(42);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(28);
    }
  }
  // .call__list
  &__list {
    display: flex;
    //justify-content: center;
    @include breakpoint.small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }
  // .call__item
  &__item {
    font-size: mini.rem(16);
    display: flex;
    &:not(:last-child) {
      margin-right: mini.rem(50);
      @include breakpoint.small-tablet {
        margin-right: 0;
        margin-bottom: mini.rem(18);
      }
    }
    @include breakpoint.mobile {
      max-width: mini.rem(260);
    }
  }
  // .call__item--num
  &__item--num {
    white-space: nowrap;
    font-size: mini.rem(18);
    .call__icon {
      margin-top: mini.rem(7)
    }
  }
  // .call__icon
  &__icon {
    flex-shrink: 0;
    width: mini.rem(16);
    height: mini.rem(16);
    margin-right: mini.rem(10);
    margin-top: mini.rem(6);
  }
  // .call__link
  &__link {
    font-size: inherit;
    font-weight: 500;
    line-height: 187%;
    color: var(--main-color);

  }
}
