@use "./mixins/breakpoint";

// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: calc(1265 / 16 * 1rem);
  --container-offset: calc(60 / 16 * 1rem);
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --scroll-bar-width: 0px;

  // colors
  --light-color: #ffffff;
  --main-color: #000000;
  --second-color: #EAEAEA;
  --accent-color: #F45050;
  --pressed-color: #DF3B3B;
  --bg-color: #F6F6F6;


  //--swiper-theme-color: var(--accent-text-color);
  //--swiper-pagination-bullet-inactive-color: #c6cad6;
  //--swiper-pagination-bullet-inactive-opacity: 1;
  //--swiper-pagination-bullet-size: calc(6 / 16 * 1rem);
  //--swiper-pagination-bullet-horizontal-gap: calc(3 / 16 * 1rem);
  //--swiper-navigation-size: calc(35 / 16 * 1rem);


  @include breakpoint.tablet {
    --container-offset: calc(30 / 16 * 1rem);
  }
  @include breakpoint.mobile {
    --container-offset: calc(20 / 16 * 1rem);
  }
}
