@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.collections {
  // .collections__container
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__button {
    min-width: 0;
    position: absolute;
    z-index: 10;
    font-size: mini.rem(50);
    padding: mini.rem(33) mini.rem(50);
    @include breakpoint.tablet {
      font-size: mini.rem(40);
      padding: mini.rem(20) mini.rem(36);
    }
    @include breakpoint.small-tablet {
      font-size: mini.rem(34);
    }
    @include breakpoint.mobile {
      max-width: mini.rem(210);
      padding: mini.rem(10) mini.rem(15);
    }
  }
  // .collections__swiper
  &__swiper.swiper--main {

    @include breakpoint.tablet {
      height: mini.rem(500);
    }
    @include breakpoint.mobile {
      height: mini.rem(470);
    }
  }
  .swiper__title {
    @include breakpoint.tablet {
      max-width: mini.rem(300);
    }
    @include breakpoint.small-tablet {
      max-width: mini.rem(200);
    }
  }
  .swiper-pagination {
    opacity: 0;
    pointer-events: none;
  }
}