@use "../mixins/mini";
@use "../mixins/breakpoint";

.favourites {
  // .favourites__container
  &__container {
  }

  // .favourites__breadcrumbs
  &__breadcrumbs {
  }

  // .favourites__title
  &__title {
  }

  // .favourites__list
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: mini.rem(48) mini.rem(40);
    margin-bottom: mini.rem(76);

    > * {
      justify-self: center;
    }

    @media (max-width: mini.rem(1500)) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(48) mini.rem(20);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(42);
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: mini.rem(56);
    }
  }

  // .favourites__item
  &__item {
    @include breakpoint.small-tablet {
      max-width: mini.rem(420);
    }
  }

  // .favourites__card
  &__card {
    .swiper-pagination-bullet {
      background-image: none;
      width: 25%;
      border-radius: 0;
      border-bottom: mini.rem(1) solid var(--main-color);
      opacity: 0.1;
      transition: opacity 0.3s ease 0s;
      &-active {
         opacity: 1;
      }
      @media (any-hover: hover) {
         &:hover {
            opacity: 0.5;
         }
      }
    }
  }


  .pages__button {
    display: flex;
  }
  .swiper-slide {
    width: 100% !important;
  }
}

