@use '../mixins/mini';
@use '../mixins/breakpoint';
@use '../mixins/burger';
@use '../mixins/checkbox';

.logo {
   display: flex;
   transition: opacity 0.3s ease 0s;
   @media (any-hover: hover) {
      &:hover {
         opacity: 0.4;
      }
   }
   &__picture {
      &--light {
         display: none;
      }
   }
   &__image {
      width: mini.rem(142);
      transition: width 0.3s ease 0s;
      &--light {
         display: none;
      }
      @include breakpoint.tablet {
         width: mini.rem(120);
      }
      @include breakpoint.small-tablet {
         width: mini.rem(94);
      }
      @include breakpoint.mobile {
         width: mini.rem(64);
      }
   }
}

.button {
   font-family: 'Bebas Neue Cyrillic', sans-serif;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: mini.rem(14);
   line-height: 143%;
   color: var(--light-color);
   background: var(--main-color);
   padding: mini.rem(11) mini.rem(25) mini.rem(9);
   min-width: mini.rem(227);
   min-height: mini.rem(40);
   border-radius: 0;
   transition: background 0.3s ease 0s, color 0.3s ease 0s, border 0.3s ease 0s;
   border: none;
   cursor: pointer;
   text-transform: uppercase;

   &__icon {
      stroke: var(--light-color);
      fill: var(--light-color);
      transition: stroke 0.3s ease 0s, fill 0.3s ease 0s;
      margin-bottom: mini.rem(3);
      &:first-child {
         margin-right: mini.rem(8);
      }
      &:last-child {
         margin-left: mini.rem(8);
      }
      &--down {
         transform: rotate(90deg);
      }
   }

   @media (any-hover: hover) {
      &:hover {
         background-color: var(--accent-color);
      }
   }

   &:focus {
      outline: none;
      background: var(--pressed-color);
   }

   &--big {
      font-size: mini.rem(22);
      padding: mini.rem(21) mini.rem(25) mini.rem(17);
      min-height: mini.rem(70);
      background-image: url(../img/favicon.png);
      background-repeat: no-repeat;
      background-size: mini.rem(45) mini.rem(60);
      background-position: center;
      @media (any-hover: hover) {
         &:hover {
            background-position: center;
            background-size: 0 0;
         }
      }

      &:focus {
         background-position: center;
         background-size: 0 0;
      }

      .button__icon {
         width: mini.rem(14);
         height: mini.rem(14);
         margin-bottom: mini.rem(5);
      }
   }

   &--grey, &--light {
      background: var(--second-color);
      color: var(--main-color);
      min-width: 0;

      .button__icon {
         stroke: var(--main-color);
         fill: var(--main-color);
      }

      @media (any-hover: hover) {
         &:hover {
            background: var(--main-color);
            color: var(--light-color);

            .button__icon {
               stroke: var(--light-color);
               fill: var(--light-color);
            }
         }
      }

      &:focus {
         outline: none;
         background: #282828;
         color: var(--light-color);

         .button__icon {
            stroke: var(--light-color);
            fill: var(--light-color);
         }
      }
   }
   &--light {
      background: var(--light-color);
   }

   &--blur {
      min-width: mini.rem(310);
      font-family: Didona, sans-serif;
      font-size: mini.rem(38);
      line-height: 126%;
      color: var(--main-color);
      padding: mini.rem(23) mini.rem(40) mini.rem(28);
      background: rgba(255, 255, 255, 0.7);
      backdrop-filter: blur(mini.rem(15));
      transition: background 0.3s ease 0s, color 0.3s ease 0s;
      text-transform: none;
      text-align: center;

      &::after {
         @include mini.pseudo;
         width: mini.rem(0);
         height: mini.rem(20);
         background-image: url(../img/svg/arrow-btn-light.svg);
         background-repeat: no-repeat;
         background-size: contain;
         background-position: center;
         margin-top: mini.rem(6);
         transition: width 0.3s ease 0s, margin-left 0.3s ease 0s;
      }

      @media (any-hover: hover) {
         &:hover {
            background: rgba(0, 0, 0, 0.5);
            color: var(--light-color);

            &::after {
               width: mini.rem(20);
               margin-left: mini.rem(15);
            }
         }
      }

      &:focus {
         background: rgba(0, 0, 0, 0.5);
         color: var(--light-color);

         &::after {
            width: mini.rem(20);
            margin-left: mini.rem(15);
         }
      }

      @include breakpoint.mobile {
         font-size: mini.rem(28);
         min-width: mini.rem(220);
         padding: mini.rem(11) mini.rem(25);
      }

      &::before {
         @include mini.pseudo;
         position: absolute;
         inset: 0;
         //background: linear-gradient(180deg, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0) 20%, rgba(255,255,255,0) 70%, rgba(255,255,255,0.8) 100%);
      }
   }
}

.burger {
   @include burger.burger;
   // .burger__line
   &__line {
   }
}

.control {
   margin-bottom: mini.rem(46);
   @include breakpoint.tablet {
      margin-bottom: mini.rem(36);
   }
   @include breakpoint.mobile {
      margin-bottom: mini.rem(26);
   }
   .title {
      margin-bottom: 0;
      max-width: calc(100% - mini.rem(330) * 2);
      @include breakpoint.big-desktop {
         max-width: 100%;
         margin-bottom: mini.rem(37);
      }
      @include breakpoint.mobile {
         margin-bottom: mini.rem(26);
      }
   }
}

.title {
   font-family: Didona, sans-serif;
   font-size: mini.rem(60);
   line-height: 113%;
   font-weight: 400;
   text-align: center;
   margin: 0 auto;
   max-width: mini.rem(1000);
   margin-bottom: mini.rem(46);
   @include breakpoint.small-tablet {
      font-size: mini.rem(50);
      margin-bottom: mini.rem(37);
   }
   @include breakpoint.mobile {
      font-size: mini.rem(40);
      margin-bottom: mini.rem(26);
   }

   &--h2 {
      font-size: mini.rem(50);
      line-height: 120%;
      margin-bottom: mini.rem(47);
      @include breakpoint.small-tablet {
         font-size: mini.rem(40);
         margin-bottom: mini.rem(30);
      }
      @include breakpoint.mobile {
         font-size: mini.rem(34);
         margin-bottom: mini.rem(20);
      }
   }
}

.subtitle {
   margin: 0;
   font-size: mini.rem(24);
   line-height: 133%;
   @include breakpoint.small-tablet {
      font-size: mini.rem(18);
   }
   @include breakpoint.mobile {
      font-size: mini.rem(16);
   }
}

.text {
   margin: 0;
   font-size: mini.rem(18);
   line-height: 167%;
   @include breakpoint.mobile {
      font-size: mini.rem(16);
      line-height: 150%;
   }
}

.label {
   position: relative;
   &--user {
      &::before {
         background-image: url(../img/svg/icon-user.svg);
      }
   }
   &--tel {
      &::before {
         background-image: url(../img/svg/tel.svg);
      }
   }
   &--local {
      &::before {
         background-image: url(../img/svg/location.svg);
      }
   }
   &--lock {
      &.label--alert {
         .input {
            color: var(--accent-color);
            border: mini.rem(1) solid var(--accent-color);
         }

         &::before {
            opacity: 1;
            background-image: url(../img/svg/lock-alert.svg);
         }
      }
      &::before {
         background-image: url(../img/svg/lock.svg);
      }
   }
   &--text {
      &::before {
         background-image: url(../img/svg/pencil.svg);
      }
   }
   &--email {
      &::before {
         background-image: url(../img/svg/email.svg);
      }
   }
   &--list {
      &::before {
         background-image: url(../img/svg/list.svg);
      }
   }
   &--date {
      &::before {
         background-image: url(../img/svg/date.svg);
      }
   }
   &.label--intl {
      &::before {
         display: none;
      }
      .form__button {
         position: absolute;
         height: 100%;
         width: mini.rem(40);
         left: mini.rem(25);
         display: flex;
         align-items: center;
         justify-content: space-between;
         &::before {
           @include mini.pseudo;
            width: mini.rem(18);
            height: mini.rem(14);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
         }
         .button__icon {
            margin: 0;
            fill: var(--main-color);
         }
         &[data-country="RU"] {
            &::before {
               background-image: url(../img/svg/ru.svg);
            }
         }
         &[data-country="BY"] {
            &::before {
               background-image: url(../img/svg/by.svg);
            }
         }
         &[data-country="KZ"] {
            &::before {
               background-image: url(../img/svg/kz.svg);
            }
         }
      }
      .input {
         padding: mini.rem(15) mini.rem(25) mini.rem(15) mini.rem(86);
      }
   }
   &.label--clear {
      .input {
         padding: mini.rem(15) mini.rem(25) mini.rem(15) mini.rem(25);
      }
   }
   &::before {
      @include mini.pseudo;
      position: absolute;
      width: mini.rem(12);
      height: mini.rem(12);
      left: mini.rem(25);
      top: mini.rem(23);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      opacity: 0.3;
      transition: opacity 0.3s ease 0s;
   }
   @media (any-hover: hover) {
      &:hover {
         &::before {
            opacity: 1;
         }
      }
   }
   &:has(input:focus) {
      &::before {
         opacity: 1;
      }
   }
   &.label--active {
      &::before {
         opacity: 1;
      }
   }
}

.input {
   -webkit-appearance: none;
   appearance: none;
   border: mini.rem(1) solid transparent;
   background: var(--light-text-color);
   color: var(--dark-text-color);
   padding: mini.rem(18) mini.rem(25) mini.rem(18) mini.rem(45);
   min-height: mini.rem(60);
   border-radius: 0;
   font-size: mini.rem(14);
   line-height: 143%;
   min-width: mini.rem(260);
   resize: none;
   &::placeholder {
      color: rgba(0, 0, 0, 0.3);
      transition: color 0.3s ease 0s;
   }

   &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
   }
   &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
   }
   &::-webkit-search-decoration {
      display: none;
   }
   &::-webkit-search-cancel-button {
      display: none;
   }
   &::-webkit-search-results-button {
      display: none;
   }
   &::-webkit-search-results-decoration {
      display: none;
   }
   &::-webkit-calendar-picker-indicator {
      display: none;
   }
   &::-moz-calendar-picker-indicator {
      display: none;
   }
   &::-ms-clear {
      display: none;
   }

   @media (any-hover: hover) {
      &:hover {
         outline: none;
         border: mini.rem(1) solid rgba(0, 0, 0, 0.25);

         &::placeholder {
            color: var(--dark-text-color);
         }
      }
   }

   &:focus,
   &.form__input--filled {
      outline: none;
      border: mini.rem(1) solid rgba(0, 0, 0, 0.25);

      &::placeholder {
         color: var(--dark-text-color);
      }
   }
}

.checkbox {
   @include checkbox.checkbox;
   // .checkbox__input
   &__input {
   }
   // .checkbox__box
   &__box {
      position: relative;
      margin-right: mini.rem(10);
   }
   &__text {
      font-size: mini.rem(13);
   }
   // .checkbox__content
   &__content {
      font-size: mini.rem(14);
      line-height: 143%;
      color: var(--light-line-color);
   }

   &__link {
      color: inherit;
   }
}

.form {
   &__label {
      &:not(:last-child) {
         margin-bottom: mini.rem(10);
      }
   }
   &__body {
      display: flex;
      flex-direction: column;
      margin-bottom: mini.rem(32);
      @include breakpoint.mobile {
         margin-bottom: mini.rem(17);
      }
   }
   &__descriptions {
      margin: 0;
      font-size: mini.rem(13);
      line-height: 138%;
      text-align: center;
      margin-bottom: mini.rem(32);
      color: rgba(0, 0, 0, 0.5);
      @include breakpoint.mobile {
         margin-bottom: mini.rem(14);
      }
      a {
         color: inherit;
         text-decoration: underline;
         transition: color 0.3s ease 0s;
         @media (any-hover: hover) {
           &:hover {
              color: var(--accent-color);
           }
         }
      }
   }
   &--subscribe {
      .label {
         position: relative;
         display: flex;
         width: 100%;
         max-width: mini.rem(370);
         @media (any-hover: hover) {
            &:hover {
               .input {
                  border: mini.rem(1) solid rgba(0, 0, 0, 0.25);
               }
            }
         }
      }

      .input {
         width: 100%;
         min-width: 0;
         padding: 0 mini.rem(24);
         background: var(--light-color);
         min-height: mini.rem(50);
      }

      .button {
         position: absolute;
         top: 0;
         right: 0;
         margin: mini.rem(5);
      }
   }
   // .form__dropdown
   &__dropdown {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: mini.rem(10) 0;
      background-color: var(--bg-color);
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      &--hidden {
         display: none;
      }
   }
   // .form__option
   &__option {
      padding: mini.rem(8) mini.rem(25);
      display: flex;
      align-items: center;
      cursor: pointer;
      width: 100%;
      text-align: left;
      transition: color 0.3s ease 0s;
      font-size: mini.rem(16);
      @media (any-hover: hover) {
        &:hover {
           color: rgba(0, 0, 0, 0.4);
        }
      }
      &:first-child {
         &::after {
           @include mini.pseudo;
            width: mini.rem(12);
            height: mini.rem(18);
            transform: rotate(-90deg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            background-image: url(../img/svg/arrow.svg);
            margin-left: auto;
         }
      }
      &::before {
         @include mini.pseudo;
         width: mini.rem(18);
         height: mini.rem(14);
         background-repeat: no-repeat;
         background-size: contain;
         background-position: center;
         margin-right: mini.rem(10);
      }
      &[data-country="RU"] {
         &::before {
            background-image: url(../img/svg/ru.svg);
         }
      }
      &[data-country="BY"] {
         &::before {
            background-image: url(../img/svg/by.svg);
         }
      }
      &[data-country="KZ"] {
         &::before {
            background-image: url(../img/svg/kz.svg);
         }
      }
   }
}

.swiper {
   &--main {
      .swiper-slide {
         position: relative;
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: center;

         &__play {
            justify-content: flex-end;

            .swiper__title {
               margin-bottom: mini.rem(130);
               @include breakpoint.small-tablet {
                  margin-bottom: mini.rem(90);
               }
               @include breakpoint.mobile {
                  margin-bottom: mini.rem(76);
               }
            }

            &::before {
               display: none;
            }

         }

         &:not(.play--light) .play__button {
            background-image: url(../img/svg/play.svg);
            background-color: rgba(255, 255, 255, 0.8);

            &::before,
            &::after {
               border: mini.rem(1) solid var(--light-color);
            }
            &--active {
               background-image: url(../img/svg/play-light.svg);
               background-color: rgba(255, 255, 255, 0.08);

               &::before,
               &::after {
                  border: mini.rem(1) solid rgba(255, 255, 255, 0.5);
               }
            }

            @media (any-hover: hover) {
               &:hover {
                  background-image: url(../img/svg/play-light.svg);
                  background-color: rgba(255, 255, 255, 0.08);

                  &::before,
                  &::after {
                     border: mini.rem(1) solid rgba(255, 255, 255, 0.5);
                  }
               }
            }
         }

         &::after {
            @include mini.pseudo;
            position: absolute;
            inset: 0;
            z-index: 1;
            pointer-events: none;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0) 80%, rgba(255, 255, 255, 0.7) 100%);
         }
      }
      .swiper__logo {
         margin-bottom: mini.rem(16);
         @include breakpoint.mobile {
            margin-bottom: mini.rem(18);
         }
      }
      .swiper__logo-image {

      }
      // .swiper__title
      .swiper__title {
         margin: 0;
         font-family: Didona, sans-serif;
         font-size: mini.rem(50);
         line-height: 120%;
         text-align: center;
         z-index: 5;
         @include breakpoint.small-tablet {
            font-size: mini.rem(34);
         }
      }
      .swiper__subtitle {
         margin: 0;
         font-size: mini.rem(24);
         line-height: 133%;
         text-align: center;
         @include breakpoint.mobile {
            font-size: mini.rem(18);
            line-height: 144%;
         }
      }
      // .swiper__picture
      .swiper__picture {
         position: absolute;
         height: 100%;
         width: 100%;
      }

      .swiper__image,
      .play__video {
         position: absolute;
         height: 100%;
         width: 100%;
         object-position: top center;
         z-index: -1;
         object-fit: cover;
      }
      .play__button {
         z-index: 20;
      }
      .swiper__link {
         z-index: 10;
         &::before {
            position: absolute;
            @include mini.pseudo;
            inset: 0;
         }
      }
   }
   .swiper-pagination-bullet {
      background-image: url(../img/favicon.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      border-radius: 0;
      transition: opacity 0.3s ease 0s;
      @media (any-hover: hover) {
         &:hover:not(&-active) {
            opacity: 0.5;
         }
      }
   }
   .swiper-button-next,
   .swiper-button-prev {
      width: mini.rem(96);
      opacity: 0.5;
      @include breakpoint.mobile {
         width: mini.rem(67);
      }
      @media (any-hover: hover) {
         &:hover {
            opacity: 1;
         }
      }
      &:focus {
         opacity: 1;
      }
   }
   .swiper-button-prev {
      transform: scaleX(-1);
   }
   .swiper-button-next:after,
   .swiper-rtl .swiper-button-prev:after,
   .swiper-button-prev:after,
   .swiper-rtl .swiper-button-next:after {
      content: '';
      width: mini.rem(24);
      height: mini.rem(24);
      background-image: url(../img/svg/arrow-btn-light.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
   }
   .swiper-button-next:before,
   .swiper-rtl .swiper-button-prev:before,
   .swiper-button-prev:before,
   .swiper-rtl .swiper-button-next:before {
      @include mini.pseudo;
      width: mini.rem(57);
      height: mini.rem(70);
      background-image: url(../img/lion.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      margin-right: mini.rem(15);
      @include breakpoint.mobile {
         width: mini.rem(33);
         height: mini.rem(40);
      }
   }

   &--light {
      .swiper-slide {
         &::after {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0.6) 100%);
         }
      }
      .swiper-pagination-bullet {
         background-image: url(../img/favicon-light.png);
      }
   }
}

:root {
   --swiper-pagination-bullet-size: calc(15 / 16 * 1rem);
   --swiper-pagination-bullet-height: calc(20 / 16 * 1rem);
   --swiper-pagination-color: transparent;
   --swiper-pagination-bullet-border: none;
   --swiper-pagination-bullet-horizontal-gap: calc(8 / 16 * 1rem);
   --swiper-pagination-bullet-inactive-color: transparent;
   --swiper-pagination-bullet-inactive-opacity: 0.3;
   --swiper-pagination-bottom: calc(60 / 16 * 1rem);
   //--swiper-navigation-size: calc(96 / 16 * 1rem);
   --swiper-navigation-height: calc(70 / 16 * 1rem);
   --swiper-navigation-sides-offset: calc(20 / 16 * 1rem);
   @include breakpoint.small-tablet {
      --swiper-pagination-bottom: calc(40 / 16 * 1rem);
   }
   @include breakpoint.mobile {
      --swiper-pagination-bullet-horizontal-gap: calc(5 / 16 * 1rem);
      --swiper-pagination-bottom: calc(30 / 16 * 1rem);
      --swiper-navigation-sides-offset: calc(10 / 16 * 1rem);
   }
}

.play {
   width: 100%;
   height: 100%;
   position: relative;
   display: flex;
   align-items: center;
   justify-content: center;

   &__video {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }

   &:has(video) {
      &::before {
         position: absolute;
         @include mini.pseudo;
         inset: 0;
         background: var(--main-color);
         opacity: 0.5;
         z-index: 1;
         pointer-events: none;
         transition: opacity 0.3s ease 0s;
      }
   }

   &:has(&__button--active) {
      &::before {
         opacity: 0;
      }
   }

   @media (any-hover: hover) {
      &:has(&__button:hover) {
         &::before {
            opacity: 0;
         }
      }
   }

   &__button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: mini.rem(140);
      height: mini.rem(140);
      background-image: url(../img/svg/play-light.svg);
      background-repeat: no-repeat;
      background-size: mini.rem(50) mini.rem(50);
      background-position: 55% center;
      background-color: rgba(255, 255, 255, 0.08);
      backdrop-filter: blur(mini.rem(15));
      border-radius: 100%;
      opacity: 1;
      transition: background-color 0.3s ease 0s, opacity 0.3s ease 0s;
      z-index: 1;

      &--active {
         background-image: url(../img/svg/play.svg);
         background-color: rgba(255, 255, 255, 0.8);

         &::before,
         &::after {
            border: mini.rem(1) solid var(--light-color);
         }
      }

      @media (any-hover: hover) {
         &:hover {
            background-image: url(../img/svg/play.svg);
            background-color: rgba(255, 255, 255, 0.8);

            &::before,
            &::after {
               border: mini.rem(1) solid var(--light-color);
            }
         }
      }
      @include breakpoint.mobile {
         width: mini.rem(120);
         height: mini.rem(120);
      }

      &::before,
      &::after {
         @include mini.pseudo;
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         border-radius: 50%;
         pointer-events: none;
         border: mini.rem(1) solid rgba(255, 255, 255, 0.5);
      }

      &::before {
         animation: btnWave 1.5s infinite linear;
      }

      &::after {
         animation: btnWave 1.5s infinite linear 0.75s;
      }
   }

   &--light {
      .swiper__title {
         color: var(--light-color);
      }
   }
}

@keyframes btnWave {
   from {
      transform: scale(1);
      opacity: 1;
   }
   to {
      transform: scale(2);
      opacity: 0;
   }
}

.breadcrumbs {
   position: relative;
   margin: mini.rem(16) 0 mini.rem(30);
   padding-left: mini.rem(22);
   @include breakpoint.small-tablet {
      margin: mini.rem(26) 0 mini.rem(17);
   }
   @include breakpoint.mobile {
      padding-left: mini.rem(18);
      margin: mini.rem(16) 0 mini.rem(23);
   }
   // .breadcrumbs__container
   &__container {
   }

   // .breadcrumbs__list
   &__list {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
   }

   // .breadcrumbs__item
   &__item {
      display: flex;
      align-items: center;
      font-size: mini.rem(13);
      line-height: 154%;
      @include breakpoint.mobile {
         font-size: mini.rem(12);
      }

      &:first-child {
         position: absolute;
         top: mini.rem(4);
         left: 0;
         @include breakpoint.mobile {
            top: mini.rem(3);
         }

         .breadcrumbs__link {
            display: inline-block;
            width: mini.rem(12);
            height: mini.rem(12);
            background-image: url(../img/svg/home-line.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
         }
      }

      &:not(:first-child) {
         &::before {
            opacity: 0.3;
            content: '-';
            display: inline-block;
            margin-right: mini.rem(10);
            @include breakpoint.mobile {
               margin-right: mini.rem(6);
            }
         }

         .breadcrumbs__link {
            opacity: 0.3;
            @media (any-hover: hover) {
               &:hover {
                  opacity: 1;
               }
            }
         }
      }

      &:not(:last-child) {
         margin-right: mini.rem(10);
         @include breakpoint.mobile {
            margin-right: mini.rem(6);
         }
      }

      &:last-child {
         &::before {
            opacity: 1;
         }

         opacity: 0.3;
      }
   }

   // .breadcrumbs__link
   &__link {
      color: inherit;
      transition: opacity 0.3s ease 0s;
   }
}

.pages {
   position: relative;
   display: flex;
   align-items: center;
   justify-content: center;
   margin-bottom: mini.rem(100);
   @include breakpoint.tablet {
      margin-bottom: mini.rem(60);
   }
   @include breakpoint.mobile {
      flex-direction: column;
      align-items: stretch;
      margin-bottom: mini.rem(50);
   }
   // .pages__list
   &__list {
      display: flex;
      align-items: center;
      @include breakpoint.mobile {
         margin-bottom: mini.rem(30);
         align-self: center;
      }
   }

   // .pages__item
   &__item {
      text-align: center;
      font-size: mini.rem(16);

      &:first-child {
         transform: rotate(180deg);
         margin-right: mini.rem(30);
      }

      &:last-child {
         margin-left: mini.rem(15);
      }

      &:not(:last-child):not(&:first-child) {
         margin-right: mini.rem(15);
      }

      &:not(&--active) {
         &::before {
            opacity: 0.15;
         }
      }

      &:not(&--active):not(&--dots) {
         @media (any-hover: hover) {
            &:hover {
               &::before {
                  opacity: 0.5;
               }
            }
         }
      }

      &:not(&--arrow) {
         position: relative;
         // .pages__link
         .pages__link {
            font-family: 'Bebas Neue Cyrillic', sans-serif;
            position: relative;
            top: mini.rem(5);
            width: mini.rem(30);
            height: mini.rem(40);
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: inherit;
            line-height: 125%;
            text-align: center;
            color: var(--main-color);
         }

         &::before {
            position: absolute;
            top: 0;
            left: 0;
            @include mini.pseudo;
            width: 100%;
            height: 100%;
            background-image: url(../img/pages.png);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            z-index: -1;
            transition: opacity 0.3s ease 0s;
         }
      }

      &--active:not(&--arrow) {
         //background-image: url(../img/pages-dark.png);
         width: mini.rem(30);
         height: mini.rem(40);
         color: var(--light-color);
         line-height: 290%;
      }
   }

   &__link {
      display: inline-block;
      width: 100%;
      height: 100%;
   }

   // .pages__item--arrow
   &__item--arrow {
      width: mini.rem(24);
      height: mini.rem(24);
      background-image: url(../img/svg/arrow-btn.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      opacity: 0.15;
      transition: opacity 0.3s ease 0s;
      @media (any-hover: hover) {
         &:hover {
            opacity: 0.5;
         }
      }
   }

   // .pages__item--dots
   &__item--dots {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      vertical-align: bottom;
      width: mini.rem(30);
      height: mini.rem(40);
      font-size: mini.rem(14);
      line-height: 250%;
      text-align: center;
   }

   &__button {
      left: 0;
      position: absolute;
      min-width: mini.rem(150);
      display: none;

      @include breakpoint.small-tablet {
         display: flex;
         position: inherit;
         margin-right: mini.rem(45);
      }
      @include breakpoint.mobile {
         margin-right: 0;
         order: 1;
      }
   }
}

.price {
   display: flex;
   align-items: center;
   justify-content: center;
   text-align: center;
   // .price__sell
   &__sell {
      font-weight: 800;
      font-size: mini.rem(18);
      color: var(--light-color);
      line-height: 133%;
      padding: mini.rem(3) mini.rem(10);
      background: var(--accent-color);
      &:not(:last-child) {
         margin-right: mini.rem(15);
      }
   }

   // .price__cost
   &__cost {
      font-size: mini.rem(26);
      line-height: 115%;
   }

   // .price__cost--old
   &__cost--old {
      font-size: mini.rem(20);
      font-weight: 500;
      text-decoration: line-through;
      opacity: 0.5;
      &:not(:last-child) {
         margin-right: mini.rem(10);
      }
   }
}

.card {
   position: relative;
   text-align: center;
   @media (any-hover: hover) {
      &:hover {
         .card__title {
            opacity: 0.5;
         }
         .swiper-pagination, .card__heart {
            opacity: 1;
         }
      }
   }
   // .card__swiper
   &__swiper {
      margin-bottom: mini.rem(25);
      z-index: 5;
      &:has(.swiper-pagination-lock) {
         pointer-events: none;
      }
      @include breakpoint.mobile {
         margin-bottom: mini.rem(17);
      }
   }
   // .card__image
   &__image {
      width: mini.rem(420);
   }
   &__image,
   picture {
      margin: 0 auto;
   }
   // .card__title
   &__title {
      font-family: Didona, sans-serif;
      margin: 0;
      font-weight: 400;
      font-size: mini.rem(30);
      line-height: 127%;
      margin-bottom: mini.rem(11);
      transition: opacity 0.3s ease 0s;
      @include breakpoint.mobile {
         font-size: mini.rem(24);
      }
   }
   // .card__link
   &__link {
      color: inherit;
      &::before {
         @include mini.pseudo;
         position: absolute;
         inset: 0;
         z-index: 1;
      }
   }
   // .card__price
   &__price {
   }
   &__remove, &__heart {
      position: absolute;
      top: mini.rem(10);
      right: mini.rem(10);
      stroke-width: mini.rem(2);
      stroke: var(--main-color);
      z-index: 10;
      transition: stroke 0.3s ease 0s, opacity 0.3s ease 0s;
      @media (any-hover: hover) {
         &:hover {
            stroke: var(--accent-color);
         }
      }
      &:focus {
         stroke: var(--accent-color);
      }
   }
   &__heart {
      top: mini.rem(20);
      left: mini.rem(20);
      right: auto;
      opacity: 0.2;
      &--active {
         opacity: 1;
         fill: var(--accent-color);
      }
   }
   &__icon {
   }
   &__status {
      position: absolute;
      top: mini.rem(15);
      right: 0;
      font-weight: 500;
      font-size: mini.rem(16);
      line-height: 187%;
      padding: mini.rem(5) mini.rem(15);
      text-align: center;
      background: var(--main-color);
      color: var(--light-color);
      pointer-events: none;
      z-index: 10;
      &--grey {
         background: var(--second-color);
         color: rgba(0, 0, 0, 0.4);
      }
   }
   .swiper-pagination {
      bottom: 0;
      opacity: 0;
      transition: opacity 0.3s ease 0s;
      @include breakpoint.small-tablet {
         opacity: 1;
      }
   }
   .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0 mini.rem(5);
   }
}

.browse {
   // .browse__list
   &__list {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(40);
      @include breakpoint.small-tablet {
         gap: mini.rem(20);
      }
      @include breakpoint.mobile {
         gap: mini.rem(10);
         grid-template-columns: repeat(1, 1fr);
      }
   }
   // .browse__item
   &__item {
      &:first-child {
         svg {
            transform: rotate(180deg);
            margin-left: 0;
            margin-right: mini.rem(10);
         }
      }
   }
   // .browse__item--button
   &__item--button {
   }
   // .browse__button
   &__button {
      height: mini.rem(60);
      background: var(--bg-color);
      @media (any-hover: hover) {
         &:hover {
            svg {
               opacity: 1;
            }
         }
      }
      &:focus {
         opacity: 1;
      }
      svg {
         width: mini.rem(24);
         height: mini.rem(24);
         margin-left: mini.rem(10);
         opacity: 0.15;
      }
   }
}

.info {
   position: relative;
   font-size: mini.rem(13);
   line-height: 138%;
   color: rgba(0, 0, 0, 0.5);
   display: inline-block;
   padding-left: mini.rem(30);
   &::before {
     @include mini.pseudo;
      position: absolute;
      width: mini.rem(20);
      height: mini.rem(20);
      left: 0;
      right: 0;
      background-image: url(../img/svg/info.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      @include breakpoint.small-tablet {
         opacity: 0.15;
      }
   }
}

.value {
   display: flex;
   flex-wrap: wrap;
   // .value__discount
   &__discount {
      position: relative;
      font-weight: 800;
      font-size: mini.rem(18);
      line-height: 133%;
      text-align: center;
      background: var(--accent-color);
      display: flex;
      justify-content: center;
      align-items: center;
      height: mini.rem(30);
      padding: 0 mini.rem(8);
      color: var(--light-color);
      white-space: nowrap;
      &::after {
         @include mini.pseudo;
         position: absolute;
         right: mini.rem(-12);
         top: 0;
         width: 0;
         height: 0;
         border-top: mini.rem(15) solid transparent;
         border-bottom: mini.rem(15) solid transparent;
         border-left: mini.rem(12) solid var(--accent-color);
      }
   }
   // .value__old
   &__old {
      font-size: mini.rem(26);
      line-height: 115%;
      text-decoration: line-through;
      color: rgba(0, 0, 0, 0.5);
      white-space: nowrap;
      @include breakpoint.mobile {
         font-size: mini.rem(20);
         font-weight: 500;
      }
   }
   // .value__price
   &__price {
      font-size: mini.rem(26);
      line-height: 115%;
      white-space: nowrap;
      @include breakpoint.mobile {
         font-size: mini.rem(20);
         font-weight: 500;
      }
   }
}