.footer {
  background: var(--bg-color);
}
.footer__top {
  padding: 4.6875rem 0 5.3125rem;
  display: grid;
  border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.08);
}
@media (max-width: 60rem) {
  .footer__top {
    padding: 4.0625rem 0 3.4375rem;
  }
}
@media (max-width: 48rem) {
  .footer__top {
    padding: 3.125rem 0 2.5rem;
  }
}
.footer__top .container {
  display: grid;
  grid-template-columns: 83.5% 1fr;
  gap: 0.9375rem;
}
.footer__top .container > * {
  align-self: start;
}
@media (max-width: 60rem) {
  .footer__top .container {
    grid-template-columns: 1fr 20%;
  }
}
@media (max-width: 48rem) {
  .footer__top .container {
    grid-template-columns: 1fr;
    gap: 1.875rem;
  }
}
.footer__nav {
  display: grid;
  grid-template-columns: 36% 1fr;
  gap: 0.9375rem;
}
.footer__nav > * {
  align-self: start;
}
@media (max-width: 60rem) {
  .footer__nav {
    grid-template-columns: 30% 1fr;
  }
}
@media (max-width: 48rem) {
  .footer__nav {
    grid-template-columns: repeat(2, auto);
    gap: 1.875rem;
  }
}
.footer__title {
  margin: 0;
  font-family: "Bebas Neue Cyrillic", sans-serif;
  font-size: 1.125rem;
  line-height: 111%;
  color: var(--main-color);
  text-transform: uppercase;
  margin-bottom: 1.25rem;
}
.footer__list {
  flex-direction: column;
  display: flex;
  flex-wrap: wrap;
  max-height: 11.875rem;
  align-content: start;
}
@media (max-width: 48rem) {
  .footer__list {
    align-content: stretch;
    gap: 0 1.875rem;
  }
}
.footer__item {
  min-width: 39%;
}
@media (max-width: 60rem) {
  .footer__item {
    min-width: 50%;
  }
}
@media (max-width: 48rem) {
  .footer__item {
    min-width: 0;
    max-width: 50%;
  }
}
.footer__link {
  display: inline-block;
  font-size: 1rem;
  padding: 0.4375rem 0;
  color: #000;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .footer__link:hover {
    color: var(--accent-color);
  }
}
.footer__contact .footer__list {
  flex-direction: row;
}
.footer__link--tel {
  font-weight: 600;
}
.footer__link--mail {
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.5);
}
.footer__bottom .container {
  display: grid;
  grid-template-columns: 11% 18% 1fr 32% 12.0625rem;
  gap: 0.9375rem;
}
.footer__bottom .container > * {
  align-self: center;
}
.footer__bottom .container > *:not(.footer__description) {
  grid-row: 1/3;
}
@media (max-width: 60rem) {
  .footer__bottom .container > *:not(.footer__description) {
    grid-row: auto;
  }
}
@media (max-width: 60rem) {
  .footer__bottom .container {
    margin: 3.125rem 0 2.5rem;
    grid-template-columns: 24% 28% 1fr;
    gap: 2.5rem 0.9375rem;
  }
}
@media (max-width: 48rem) {
  .footer__bottom .container {
    margin: 2.5rem 0;
    grid-template-columns: repeat(1, 1fr);
    gap: 0.375rem;
  }
  .footer__bottom .container > * {
    justify-self: center;
  }
}
.footer__logo {
