  }
}
@media (max-width: 48rem) {
  .footer__description:not(:last-child) {
    justify-self: center;
  }
}
@media (any-hover: hover) {
  .footer__description:hover {
    color: var(--accent-color);
  }
}
@media (max-width: 60rem) {
  .footer .form {
    width: 100%;
