@use "../mixins/mini";
@use "../mixins/breakpoint";

.error {
  background-image: url('../img/404-bg.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom right;
  padding: mini.rem(130) 0;
  @include breakpoint.tablet {
    background-image: url('../img/404-bg-mobile.png');
    background-position: bottom center;
    padding: mini.rem(70) 0 mini.rem(470);
  }
  @include breakpoint.mobile {
    padding: mini.rem(50) 0 mini.rem(280);
  }
  // .404__container
  &__container {
  }

  &__image {
    width: 100%;
    max-width: mini.rem(547);
    margin-bottom: mini.rem(43);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(29);
    }
  }

  // .404__title
  &__title {
    margin-bottom: mini.rem(16);
    text-align: left;
    @include breakpoint.mobile {
      text-align: center;
    }
  }

  // .404__text
  &__text {
    margin-bottom: mini.rem(50);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(40);
      text-align: center;
    }
  }

  // .404__button
  &__button {
    .button__icon {
      //margin-left: 0;
      //margin-right: mini.rem(8);
      //margin-bottom: mini.rem(4);
      transform: rotate(180deg);
    }
    @include breakpoint.mobile {
      width: 100%;
    }
  }
}