@use "../mixins/mini";
@use "../mixins/breakpoint";

.steps {
  margin-bottom: mini.rem(60);
  @include breakpoint.small-tablet {
    margin-bottom: mini.rem(120);
  }
  @include breakpoint.mobile {
    margin-bottom: mini.rem(60);
  }
  // .steps__container
  &__container {
  }
  // .steps__title
  &__title {
  }
  // .steps__list
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: mini.rem(40);
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(50);
      grid-template-columns: repeat(1, 1fr);
    }
    @include breakpoint.mobile {
      gap: mini.rem(36);
    }
  }
  // .steps__item
  &__item {
    &:nth-child(3n) {
      @include breakpoint.tablet {
        grid-column: 1/3;
      }
      @include breakpoint.small-tablet {
        grid-column: auto;
      }
    }
  }
  // .steps__head
  &__head, &__play {
    width: auto;
    height: auto;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    padding-top: 90%;
    overflow: hidden;
    @include breakpoint.tablet {
      padding-top: mini.rem(300);
    }
    @include breakpoint.small-tablet {
      padding-top: 56%;
    }
  }
  // .steps__image
  &__image, picture, .play__video {
    inset: 0;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  &__head, &__play {
    margin-bottom: mini.rem(30);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(17);
    }
  }
  // .steps__content
  &__content {
    @include breakpoint.desktop {
      padding: 0 mini.rem(30);
    }
    @include breakpoint.big-desktop {
      padding: 0;
    }
    @include breakpoint.tablet {
      padding: 0 mini.rem(30);
    }
    @include breakpoint.mobile {
      padding: 0;
    }
  }
  // .steps__caption
  &__caption {
    margin: 0;
    font-weight: 400;
    font-size: mini.rem(30);
    line-height: 127%;
    margin-bottom: mini.rem(11);
    @include breakpoint.mobile {
      font-size: mini.rem(24);
      margin-bottom: mini.rem(6);
    }
  }
  // .steps__text
  &__text {
    opacity: 0.6;
  }
  // .steps__play
  &__play {
    height: auto;
  }
}