@use "../mixins/mini";
@use "../mixins/breakpoint";

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  //background: var(--light-color);
  padding: mini.rem(25) 0;
  transition: padding 0.3s ease 0s, background 0.3s ease 0s;
  @include breakpoint.small-tablet {
    padding: mini.rem(20) 0;
  }
  @include breakpoint.mobile {
    padding: mini.rem(10) 0;
  }

  &--slim {
    background: var(--bg-color);
    padding: mini.rem(15) 0;
    @include breakpoint.small-tablet {
      padding: mini.rem(20) 0;
    }
    @include breakpoint.mobile {
      padding: mini.rem(10) 0;
    }

    .logo__image {
      width: mini.rem(96);
      @include breakpoint.small-tablet {
        width: mini.rem(94);
      }
      @include breakpoint.mobile {
        width: mini.rem(64);
      }
    }

    .header__container {
      transition: min-height 0.3s ease 0s;
      min-height: mini.rem(62);
      @include breakpoint.small-tablet {
        min-height: mini.rem(60);
      }
      @include breakpoint.mobile {
        min-height: mini.rem(42);
      }
    }

    & + .main {
      //margin-top: mini.rem(92);
      //@include breakpoint.small-tablet {
      //  margin-top: mini.rem(100);
      //}
      //@include breakpoint.mobile {
      //  margin-top: mini.rem(62);
      //}
    }
  }
  &--light {
    .logo__picture, .logo__image {
      display: none;
    }
    .logo__picture--light, .logo__image--light {
        display: block;
    }
    .header__icon svg {
      fill: var(--light-color);
    }
    .header__burger, .header__icon, .header__link {
      color: var(--light-color);
    }
    .header__burger.burger--active {
      color: var(--main-color);
    }
  }
  &.menu--active, &.search--active {
    .header__aside {
      opacity: 1;
      transform: translateX(0);
      background: rgba(0, 0, 0, 0.3);
    }
    & + .main {
        .breadcrumbs {
          z-index: 5;
        }
    }
  }
  &.menu--active {
    .header__aside {
      transition: transform 0.3s ease 0s, background 0.3s ease 0.3s;
    }
  }
  &.search--active {
    .header__aside {
      transition: opacity 0.3s ease 0s, background 0.3s ease 0s;
      @include breakpoint.tablet {
        background: var(--bg-color);
        z-index: -1;
      }
    }
  }

  // .header__container
  &__container {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    min-height: mini.rem(94);
    @include breakpoint.tablet {
      min-height: mini.rem(78);
    }
    @include breakpoint.small-tablet {
      min-height: mini.rem(60);
    }
    @include breakpoint.mobile {
      min-height: mini.rem(42);
    }
  }

  // .header__bar
  &__bar {
    display: flex;
    align-items: center;

    &:first-child {
      @include breakpoint.mobile {
        margin-right: mini.rem(20);
      }
    }

    @include breakpoint.mobile {
      margin-top: mini.rem(9);
    }
  }

  // .header__burger
  &__burger {
    margin-right: mini.rem(60);
    transition: opacity 0.3s ease 0s;

    &.burger--active {
      z-index: 100;
    }

    @media (any-hover: hover) {
      &:hover {
        opacity: 0.4;
      }
    }
    @include breakpoint.small-tablet {
      margin-right: mini.rem(50);
    }
    @include breakpoint.mobile {
      margin-right: mini.rem(20);
    }
  }

  // .header__icon
  &__icon {
    position: relative;
    display: flex;
    align-items: center;
    transition: opacity 0.3s ease 0s;
    font-weight: 500;
    font-size: mini.rem(14);
    color: var(--main-color);

    &:not(:last-child) {
      margin-right: mini.rem(30);
      @include breakpoint.small-tablet {
        margin-right: mini.rem(20);
      }
      @include breakpoint.mobile {
        margin-right: mini.rem(12);
      }
    }

    svg {
      width: mini.rem(24);
      height: mini.rem(24);
      fill: var(--accent-text-color);
      transition: fill 0.3s ease 0s;
      @include breakpoint.mobile {
        width: mini.rem(18);
        height: mini.rem(18);
      }
    }

    &.search--active {
      z-index: 100;
    }

    @media (any-hover: hover) {
      &:hover:not(.header__icon--count) {
        opacity: 0.4;
      }
    }

    &--count {
      @media (any-hover: hover) {
        &:hover {
          svg {
            fill: var(--accent-color);
          }
        }
      }
    }
  }

  &__detail {
    display: none;
    margin-left: mini.rem(10);
    @include breakpoint.desktop {
      display: inline;
    }
  }

  // .header__logo
  &__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    + .header__bar {
      @include breakpoint.mobile {
        width: 100%;
        .header__link {
          margin-right: auto;
        }
      }
    }
  }

  // .header__link
  &__link {
    position: relative;
    display: flex;
    align-items: center;
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-size: mini.rem(18);
    color: var(--main-color);
    align-self: flex-end;
    transition: opacity 0.3s ease 0s;
    @include breakpoint.mobile {
      top: mini.rem(4);
    }

    &:not(:last-child) {
      margin-right: mini.rem(60);
      @include breakpoint.small-tablet {
        margin-right: mini.rem(50);
      }
      @include breakpoint.mobile {
        margin-right: mini.rem(0);
      }
    }

    @media (any-hover: hover) {
      &:hover {
        opacity: 0.4;
      }
    }
  }

  // .header__count
  &__count {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: mini.rem(-3);
    right: mini.rem(-5);
    min-width: mini.rem(14);
    font-weight: 900;
    font-size: mini.rem(10);
    color: var(--light-color);
    font-variant-numeric: lining-nums;
    z-index: 1;
    padding: 0 mini.rem(2);

    &::before {
      @include mini.pseudo;
      width: 100%;
      position: absolute;
      padding-top: 100%;
      background: var(--main-color);
      border-radius: 50%;
      z-index: -1;
      transition: background 0.3s ease 0s;
    }

    &--fill {
      &::before {
        background: var(--accent-color);
      }
    }
  }

  &__aside {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    opacity: 0;
    transform: translateX(-100%);
    transition: transform 0.3s ease 0s;
    padding-top: mini.rem(90);
    overflow: hidden;
  }

  // .header__nav
  &__nav {
    display: none;

    &--active {
      display: flex;
    }
  }

  &__search {

  }
}

.search {
  display: none;
  position: fixed;
  width: calc(100% - var(--container-offset) * 2);
  max-width: mini.rem(1266);
  height: auto;
  top: var(--header-height);
  left: 50%;
  transform: translateX(-50%);
  flex-direction: column;

  &--active {
    display: flex;
  }
  &__form {
    width: 100%;
    background: var(--light-color);
  }
  // .search__label
  &__label {
    //width: 100%;
    //margin-left: mini.rem(15);
    &::before {
      left: mini.rem(54);
      width: mini.rem(24);
      height: mini.rem(24);
      background-image: url(../img/svg/icon-search.svg);
      @include breakpoint.tablet {
        left: mini.rem(34);
      }
      @include breakpoint.mobile {
        left: mini.rem(24);
      } 
    }
  }
  &__input {
    border: none;
    max-width: 100%;
    width: 100%;
    padding: mini.rem(28) mini.rem(40) mini.rem(28) mini.rem(113);
    font-size: mini.rem(18);
    @include breakpoint.tablet {
      padding: mini.rem(28) mini.rem(30) mini.rem(28) mini.rem(93);
    }
    @include breakpoint.mobile {
      padding: mini.rem(18) mini.rem(20) mini.rem(18) mini.rem(73);
      font-size: mini.rem(14);
    }
    @media (any-hover: hover) {
      &:hover {
        border: none;
      }
    }

    &:focus,
    &.form__input--filled {
      border: none;
    }
  }
  &__list {
    background: var(--light-color);
    @include breakpoint.tablet {
      margin-bottom: mini.rem(30);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(20);
    }
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    height: mini.rem(80);
    padding: mini.rem(10) mini.rem(40);
    font-size: mini.rem(16);
    line-height: 143%;
    border-top: mini.rem(1) solid var(--second-color);
    transition: background 0.3s ease 0s;
    @include breakpoint.tablet {
      padding: mini.rem(10) mini.rem(30) mini.rem(10) mini.rem(20);
    }
    @include breakpoint.mobile {
      padding: mini.rem(10) mini.rem(20) mini.rem(10) mini.rem(10);
    }
    @media (any-hover: hover) {
      &:hover {
        background: var(--bg-color);
      }
    }
  }
  &__image {
    width: mini.rem(53);
    height: mini.rem(60);
    margin-right: mini.rem(20);
    @include breakpoint.mobile {
      margin-right: mini.rem(10);
    }
  }
  &__link {
    margin-right: auto;
    color: inherit;
    @include breakpoint.mobile {
      font-size: mini.rem(14);
    }
    &::before {
      @include mini.pseudo;
      position: absolute;
      inset: 0;
    }
  }
  &__price {
    margin-left: mini.rem(20);
    flex-shrink: 0;
  }
  &__button {
    display: none;
    .button__icon {
      margin-bottom: mini.rem(3);
    }
    @include breakpoint.tablet {
      display: flex;
    }
  }
}

.nav {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  background: var(--light-color);
  @include breakpoint.small-tablet {
    width: 100%;
    overflow-y: auto;
  }
  // .nav__list
  &__list {
    background: var(--light-color);
    position: absolute;
    list-style: none;
    margin: 0;
    top: 0;
    left: 0;
    width: mini.rem(367);
    min-height: 100%;
    padding: mini.rem(90) 0 mini.rem(80);
    @media (max-width: mini.rem(calc(367 * 3))) {
      width: calc((100vw - var(--scroll-bar-width)) / 3);
    }
    @include breakpoint.small-tablet {
      position: inherit;
      margin: 0 mini.rem(30);
      width: calc(100% - (30 / 16 * 2rem));
      padding: mini.rem(60) 0 mini.rem(55);
      .nav__link {
        font-size: mini.rem(22);
      }
    }
    @include breakpoint.mobile {
      margin: 0 mini.rem(20);
      width: calc(100% - (20 / 16 * 2rem));
      padding: mini.rem(50) 0 mini.rem(45);
    }
    //height: 100%;
    .nav__list {
      display: none;
      background: var(--bg-color);
      left: mini.rem(367);
      padding-bottom: 0;
      @include breakpoint.small-tablet {
        padding-top: 0;
        width: 100%;
        margin: 0;
        //margin: 0 mini.rem(30);
        //width: calc(100% - (30 / 16 * 2rem));
        .nav__link {
          font-size: mini.rem(18);
        }
      }
      @media (max-width: mini.rem(calc(367 * 3))) {
        left: calc((100vw - var(--scroll-bar-width)) / 3);
      }
      @media (any-hover: hover) {
        .nav__item--more:hover {
          > .nav__link {
            background: var(--light-color);
          }
        }
      }

      .nav__list {
        background: var(--light-color);
        @include breakpoint.small-tablet {
          margin: 0;
          //width: 100%;
        }
        @media (any-hover: hover) {
          .nav__item--more:hover {
            > .nav__link {
              background: var(--bg-color);
            }
          }
        }
      }
    }
  }

  // .nav__item
  &__item {
    //position: relative;
    @media (any-hover: hover) {
      &:hover {
        > .nav__link::before {
          opacity: 1;
        }
      }
    }

    &--more {
      > .nav__link::after {
        @include mini.pseudo;
        flex-shrink: 0;
        width: mini.rem(36);
        height: mini.rem(18);
        background-image: url('../img/svg/arrow.svg');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center right;
        margin-left: auto;
        @include breakpoint.small-tablet {
          margin-left: 0;
          position: relative;
          left: mini.rem(8);
          width: mini.rem(18);
          transform: rotate(90deg);
          background-position: center;
          transition: transform 0.3s ease 0s;
        }
      }

      @media (any-hover: hover) {
        &:hover {
          & > .nav__list {
            display: block;
          }

          > .nav__link {
            background: var(--bg-color);
            @include breakpoint.small-tablet {
              background: transparent;
            }
          }

          > .nav__link::after {
            @include breakpoint.small-tablet {
              transform: rotate(-90deg);
            }
          }
        }
      }
    }

    &--active {
      & > .nav__list {
        display: block;
      }

      > .nav__link {
        background: var(--bg-color);
        @include breakpoint.small-tablet {
          background: transparent;
        }
      }

      > .nav__link::after {
        @include breakpoint.small-tablet {
          transform: rotate(-90deg);
        }
      }
    }
  }

  // .nav__link
  &__link {
    font-size: mini.rem(18);
    line-height: 140%;
    color: var(--main-color);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: mini.rem(10) mini.rem(60) mini.rem(10) mini.rem(18);
    margin-left: mini.rem(15);
    min-height: mini.rem(50);
    @include breakpoint.tablet {
      padding: mini.rem(10) mini.rem(30) mini.rem(10) mini.rem(18);
    }
    @include breakpoint.small-tablet {
      margin-left: 0;
      justify-content: center;
      position: relative;
      text-align: center;
    }

    &::before {
      @include mini.pseudo;
      flex-shrink: 0;
      width: mini.rem(15);
      height: mini.rem(20);
      background-image: url('../img/favicon.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      margin-right: mini.rem(12);
      opacity: 0;
      transition: opacity 0.3s ease 0s;
    }
  }
}
