@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.request {
  padding: mini.rem(73) 0 mini.rem(100);
  @include breakpoint.small-tablet {
    padding: mini.rem(60) 0 mini.rem(100);
  }
  @include breakpoint.mobile {
    padding: mini.rem(50) 0 mini.rem(80);
  }
  // .request__container
  &__container {
  }
  // .request__title
  &__title {
  }
  // .request__form
  &__form {
    max-width: mini.rem(1494);
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: mini.rem(20) mini.rem(30);
    @include breakpoint.big-desktop {
      gap: mini.rem(20) mini.rem(15);
    }
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(10) mini.rem(20);
    }
    @include breakpoint.mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .label {
    &:not(:last-child) {
      margin-bottom: 0;
    }
    &--text {
      grid-column: 4/5;
      grid-row: 1/3;
      @include breakpoint.tablet {
        grid-column: 1/2;
        grid-row: 4/6;
      }
      @include breakpoint.mobile {
        grid-row: 7/8;
      }
      .input {
        height: 100%;
        min-height: mini.rem(120);
      }
    }
    &::before {
      top: mini.rem(19);
    }
  }
  .input {
    background: var(--bg-color);
    width: 100%;
    min-width: 0;
    padding: mini.rem(15) mini.rem(25) mini.rem(15) mini.rem(45);
    min-height: mini.rem(50);
  }
  .form__descriptions {
    margin-bottom: 0;
    grid-column: 2/3;
    color: rgba(0, 0, 0, 0.3);
    align-self: center;
    text-align: right;
    margin-top: mini.rem(30);
    @include breakpoint.tablet {
      text-align: left;
      margin-top: mini.rem(4);
    }
    @include breakpoint.mobile {
      grid-column: auto;
      margin-top: mini.rem(6);
      text-align: center;
    }
    a {
      text-decoration: none;
    }
  }
  .button {
    margin-top: mini.rem(30);
    max-width: mini.rem(252);
    @include breakpoint.tablet {
      max-width: 100%;
      margin-top: mini.rem(4);
    }
  }
}