@use "../mixins/mini";
@use "../mixins/breakpoint";

.account {
  // .account__container
  &__container {
  }

  // .account__breadcrumbs
  &__breadcrumbs {
  }

  // .account__title
  &__title {
    margin-bottom: mini.rem(50);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(10);
    }
  }

  &__nav {
    display: flex;
    justify-content: center;
    border-bottom: mini.rem(1) solid var(--second-color);
    margin-bottom: mini.rem(60);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(40);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(30);
    }
  }

  &__tab {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(24);
    display: inline-block;
    padding: mini.rem(25) mini.rem(30);
    color: rgba(0, 0, 0, 0.3);
    transition: color 0.3s ease 0s;
    @include breakpoint.small-tablet {
      font-size: mini.rem(18);
      padding: mini.rem(18) mini.rem(20);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(14);
      padding: mini.rem(13) mini.rem(10);
    }

    &--active {
      color: var(--main-color);
      border-bottom: mini.rem(2) solid var(--main-color);
      pointer-events: none;
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--main-color);
      }
    }
  }

  // .account__list
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: mini.rem(48) mini.rem(40);
    margin-bottom: mini.rem(76);

    > * {
      justify-self: center;
    }

    @media (max-width: mini.rem(1500)) {
      grid-template-columns: repeat(3, 1fr);
    }

    &--subscribe {
      grid-template-columns: repeat(6, 1fr);
      @media (max-width: mini.rem(1700)) {
        grid-template-columns: repeat(5, 1fr);
      }

      @media (max-width: mini.rem(1500)) {
        grid-template-columns: repeat(4, 1fr);
      }

      @media (max-width: mini.rem(1300)) {
        grid-template-columns: repeat(3, 1fr);
      }
    }

    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(48) mini.rem(20);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(42);
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: mini.rem(56);
    }
  }

  // .account__item
  &__item {
    @include breakpoint.small-tablet {
      max-width: mini.rem(420);
    }
  }

  // .account__card
  &__card {
    height: 100%;
    display: flex;
    flex-direction: column;

    .card__title {
      @media (min-width: mini.rem(1501)) {
        font-size: mini.rem(22);
      }
    }

    .card__link {
      &::before {
        margin-bottom: mini.rem(100);
      }
    }

    .card__price {
      margin-bottom: mini.rem(24);
      @include breakpoint.mobile {
        margin-bottom: mini.rem(17);
      }
    }

    .card__button {
      max-width: mini.rem(227);
      width: 100%;
      align-self: center;

      &:not(:last-child) {
        margin-top: auto;
        margin-bottom: mini.rem(10);
      }
    }
  }

  &__form {
    width: 100%;
    max-width: mini.rem(800);
    margin: 0 auto;
    margin-bottom: mini.rem(100);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(80);
    }

    .label {
      &::before {
        top: mini.rem(19);
      }
    }

    .input {
      background: var(--light-color);
      max-width: 100%;
      width: 100%;
      padding: mini.rem(15) mini.rem(25) mini.rem(15) mini.rem(45);
      min-height: mini.rem(50);
      min-width: 0;
    }
  }

  .form__body {
    background: var(--bg-color);
    padding: mini.rem(40);
    margin-bottom: mini.rem(20);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: mini.rem(20);
    @include breakpoint.small-tablet {
      padding: mini.rem(30);
      gap: mini.rem(10) mini.rem(20);
    }
    @include breakpoint.mobile {
      grid-template-columns: repeat(1, 1fr);
      padding: mini.rem(30) mini.rem(20);
      gap: mini.rem(10);
    }
  }

  .form__bottom {
    display: flex;
    justify-content: space-between;
    margin-top: mini.rem(30);
    @include breakpoint.mobile {
      margin-top: 0;
    }
  }

  .form__button {
    min-width: mini.rem(140);

    &:not(:last-child) {
      margin-right: mini.rem(20);
    }

    @include breakpoint.mobile {
      width: 100%;
    }
  }

  .pages__button {
    display: flex;
  }

  .swiper-slide {
    width: 100% !important;
  }

  // .account__order
  &__order {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: mini.rem(30) mini.rem(40);
    margin-bottom: mini.rem(60);
    @include breakpoint.tablet {
      grid-template-columns: repeat(1, 1fr);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(20);
      margin-bottom: mini.rem(40);
    }
  }
}

.order {
  // .order__item
  &__item {
    display: flex;
    flex-direction: column;
    position: relative;
    background: var(--bg-color);
    padding: mini.rem(33) mini.rem(50) mini.rem(15) mini.rem(50);
    border: mini.rem(1) solid transparent;
    transition: border 0.3s ease 0s, background 0.3s ease 0s;
    @include breakpoint.small-tablet {
      padding: mini.rem(33) mini.rem(40) mini.rem(15) mini.rem(40);
    }
  ;
    @include breakpoint.mobile {
      padding: mini.rem(20) mini.rem(30);
    }
    @media (any-hover: hover) {
      &:hover {
        border: mini.rem(1) solid rgba(0, 0, 0, 0.25);
        background: var(--light-color);

        .button--light {
          background: var(--bg-color);
        }
      }
    }
  }

  // .order__head
  &__head {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: mini.rem(24);
    @include breakpoint.mobile {
      flex-direction: column;
      align-items: center;
      margin-bottom: mini.rem(13);
    }
  }

  // .order__title
  &__title {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: mini.rem(38);
    line-height: 126%;
    margin-right: mini.rem(20);
    @include breakpoint.mobile {
      font-size: mini.rem(24);
      margin-bottom: mini.rem(5);
      margin-right: 0;
    }
  }

  // .order__time
  &__time {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(18);
    line-height: 133%;
    text-align: right;
    color: rgba(0, 0, 0, 0.3);
  }

  // .order__gallery
  &__gallery {
    display: flex;
    align-items: center;
    margin-bottom: mini.rem(24);
    flex-wrap: wrap;
    @include breakpoint.mobile {
      margin-bottom: mini.rem(10);
      justify-content: center;
    }
  }

  &__picture {
    flex: 0 0 mini.rem(62);
    height: mini.rem(70);
    margin-bottom: mini.rem(10);

    &:not(:last-child) {
      margin-right: mini.rem(10);
    }
  }

  // .order__image
  &__image {
  }

  // .order__price
  &__price {
    margin: 0;
    font-size: mini.rem(40);
    line-height: 100%;
    margin-bottom: mini.rem(20);
    margin-top: auto;
    @include breakpoint.mobile {
      text-align: center;
    }
  }

  // .order__bottom
  &__bottom {
    display: flex;
    align-items: center;
    @include breakpoint.mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  // .order__link
  &__link {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(14);
    line-height: 143%;
    color: inherit;
    display: flex;
    align-items: center;
    transition: color 0.3s ease 0s;
    visibility: hidden;
    height: 0;
    margin-right: auto;
    @include breakpoint.tablet {
      height: auto;
      visibility: visible;
    }
    @include breakpoint.mobile {
      order: 1;
      width: 100%;
      justify-content: center;
      margin-top: mini.rem(20);
    }

    &::before {
      visibility: visible;
      @include mini.pseudo;
      position: absolute;
      inset: 0;
      z-index: 1;
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--accent-color);

        .button__icon {
          fill: var(--accent-color);
          stroke: var(--accent-color);
        }
      }
    }

    .button__icon {
      fill: var(--main-color);
      stroke: var(--main-color);
      width: mini.rem(12);
      height: mini.rem(12);
    }
  }

  &__status {
    font-family: Roboto, sans-serif;
    pointer-events: none;
    text-transform: none;
  }

  // .order__button
  &__button, &__status {
    z-index: 10;
    position: relative;
    margin-right: mini.rem(-35);
    min-width: mini.rem(123);

    &:not(:last-child) {
      margin-right: mini.rem(15);
    }

    @include breakpoint.mobile {
      margin-right: 0;
    }
  }
}

.order-modal {
  // .order-modal__head
  &__head {
    display: flex;
    align-items: center;
    margin-bottom: mini.rem(20);
    @include breakpoint.mobile {
      flex-direction: column;
    }
  }

  // .order-modal__title
  &__title {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: mini.rem(38);
    line-height: 126%;
    margin-right: mini.rem(20);
    @include breakpoint.mobile {
      font-size: mini.rem(34);
      margin-bottom: mini.rem(9);
      margin-right: 0;
    }
  }

  // .order__button
  &__button.button, &__status.button {
    width: auto;
    min-width: mini.rem(123);

    &:not(:last-child) {
      margin-right: mini.rem(15);
    }

    @include breakpoint.mobile {
      margin-right: 0;
    }
  }

  &__status.button {
    font-family: Roboto, sans-serif;
    pointer-events: none;
    text-transform: none;
    @include breakpoint.mobile {
      order: 1;
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  // .order-modal__time
  &__time {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(18);
    line-height: 133%;
    text-align: right;
    color: rgba(0, 0, 0, 0.3);
    margin-left: auto;
    @include breakpoint.mobile {
      margin-bottom: mini.rem(13);
      margin-left: 0;
    }
  }

  // .order-modal__list
  &__list {
    margin-bottom: mini.rem(25);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(20);
    }
  }

  // .order-modal__item
  &__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: mini.rem(10);
      @include breakpoint.mobile {
        align-items: flex-start;
        margin-bottom: mini.rem(8);
      }
    }
  }

  // .order-modal__picture
  &__picture {
    flex: 0 0 mini.rem(35);
    height: mini.rem(40);
    margin-right: mini.rem(20);
    @include breakpoint.mobile {
      margin-right: mini.rem(10);
    }
  }
  // .order-modal__info
  &__info {
    margin-right: mini.rem(20);
  }

  &__caption {
    margin: 0;
    font-weight: 400;
    font-size: mini.rem(14);
    line-height: 143%;
  }
  // .order-modal__count
  &__count {
    margin: 0;
    font-weight: 400;
    font-size: mini.rem(14);
    line-height: 143%;
    color: rgba(0, 0, 0, 0.3);
  }

  // .order-modal__price
  &__price {
    font-size: mini.rem(16);
    line-height: 150%;
    text-align: right;
    margin-left: auto;
    white-space: nowrap;
  }

  // .order-modal__details
  &__details {
    display: flex;
    justify-content: space-between;
    margin-bottom: mini.rem(24);
    @include breakpoint.mobile {
      flex-wrap: wrap;
      margin-bottom: mini.rem(5);
    }
  }

  // .order-modal__col
  &__col {
    margin-bottom: mini.rem(15);
    &:not(:last-child) {
      margin-right: mini.rem(20);
    }
  }

  // .order-modal__subtitle
  &__subtitle {
    margin: 0;
    font-size: mini.rem(14);
    line-height: 143%;
    color: rgba(0, 0, 0, 0.3);
    margin-bottom: mini.rem(5);
  }

  // .order-modal__text
  &__text {
    margin: 0;
    font-size: mini.rem(14);
    line-height: 143%;
  }
  // .order-modal__total
  &__total {
    margin: 0;
    font-size: mini.rem(26);
    line-height: 115%;
    white-space: nowrap;
  }

  // .order-modal__bottom
  &__bottom {
    display: flex;
    justify-content: space-between;
  }
}
