@use "../mixins/mini";
@use "../mixins/breakpoint";

.graph-modal {
  background-color: transparent;
  transition: none;

  &.is-open {
    background-color: rgba(0, 0, 0, .3);
    transition: background-color 0.3s ease 0.3s;
  }

  .fade, .fade.animate-open {
    transition: opacity 0.3s ease 0s;
  }

  .title {
    font-size: mini.rem(38);
    line-height: 116%;
    @include breakpoint.mobile {
      font-size: mini.rem(34);
    }
  }

  .form {
    max-width: mini.rem(360);
    margin: 0 auto;
  }

  .input {
    width: 100%;
    min-width: 0;
    background: var(--bg-color);
  }

  .button {
    width: 100%;
    min-width: 0;
  }
}

.graph-modal__container {
  border-radius: 0;
  width: mini.rem(500);
  padding: mini.rem(55) mini.rem(60) mini.rem(60);
  @include breakpoint.mobile {
    padding: mini.rem(50) mini.rem(30) mini.rem(60);
    width: 90%
  }

  &--basket {
    position: absolute;
    right: var(--container-offset);
    top: var(--header-height);
    width: auto;
    min-width: mini.rem(454);
    max-width: 90%;
    padding: mini.rem(46) mini.rem(65) mini.rem(65);
    @include breakpoint.small-tablet {
      padding: mini.rem(40) mini.rem(20) mini.rem(50);
    }
    @include breakpoint.mobile {
      min-width: 0;
      right: 0;
      left: 0;
    }

    .title {
      font-size: mini.rem(30);
      line-height: 127%;
      text-align: left;
      margin-bottom: mini.rem(14);
      @include breakpoint.mobile {
        font-size: mini.rem(24);
        text-align: center;
      }
    }
  }

  &--order {
    padding: mini.rem(40) mini.rem(60) mini.rem(50);
    background: var(--bg-color);
    width: mini.rem(640);
    @include breakpoint.small-tablet {
      width: 90%;
      padding: mini.rem(34) mini.rem(20) mini.rem(35);
    }
  }
}

.graph-modal__close {
  right: mini.rem(-15);
  top: mini.rem(-15);
  width: mini.rem(40);
  height: mini.rem(40);
  background-color: var(--main-color);
  background-image: url(../img/svg/close-light.svg);
  background-repeat: no-repeat;
  background-position: center;
  transition: background-color .3s ease-in-out;

  &:hover {
    background-color: var(--accent-color);
  }
}