@use "../mixins/mini";
@use "../mixins/breakpoint";

.atelier {
  // .atelier__container
  margin-bottom: mini.rem(75);
  @include breakpoint.small-tablet {
    margin-bottom: mini.rem(60);
  }
  @include breakpoint.mobile {
    margin-bottom: mini.rem(50);
  }
  //&__container {
  //  &.container--wide {
  //    padding-left: 0;
  //    padding-right: 0;
  //  }
  //}
  // .atelier__breadcrumbs
  &__breadcrumbs {
  }
  // .atelier__wrap
  &__wrap {
    position: absolute;
    width: 100%;
    max-width: mini.rem(700);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    text-align: center;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(mini.rem(15));
    padding: mini.rem(36) mini.rem(56) mini.rem(32);
    @include breakpoint.small-tablet {
      max-width: mini.rem(344);
      padding: mini.rem(25) mini.rem(25) mini.rem(32);
    }
    @include breakpoint.mobile {
      max-width: mini.rem(285);
      padding: mini.rem(20) mini.rem(30) mini.rem(30);
    }
  }
  // .atelier__title
  &__title {
    margin-bottom: mini.rem(22);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(8);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(4);
    }
  }
  // .atelier__subtitle
  &__subtitle {

  }
  // .atelier__image
  &__image {
    //position: absolute;
    //inset: 0;
    //width: 100%;
    //height: 100%;
    //z-index: -1;
  }
}