@use "../mixins/mini";
@use "../mixins/breakpoint";

.catalog {
  // .catalog__container
  &__container {
  }

  // .catalog__breadcrumbs
  &__breadcrumbs {
  }

  // .catalog__title
  &__title {
  }

  // .catalog__list
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: mini.rem(48) mini.rem(40);
    margin-bottom: mini.rem(76);
    &.catalog__list--grid {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(48) mini.rem(60);
      @media (max-width: mini.rem(1500)) {
        grid-template-columns: repeat(2, 1fr);
      }
      @include breakpoint.tablet {
        grid-template-columns: repeat(1, 1fr);
        gap: mini.rem(48) mini.rem(42);
      }
      @include breakpoint.small-tablet {
        gap: mini.rem(42);
      }
      .card__image {
        width: mini.rem(880);
      }
      .card__icon {
        width: mini.rem(40);
        height: mini.rem(40);
        @include breakpoint.small-tablet {
          width: mini.rem(30);
          height: mini.rem(30);
        }
      }
    }
    > * {
      justify-self: center;
    }

    @media (max-width: mini.rem(1500)) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(48) mini.rem(20);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(42);
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: mini.rem(56);
    }
  }

  // .catalog__item
  &__item {
    @include breakpoint.small-tablet {
      max-width: mini.rem(420);
    }
  }

  // .catalog__card
  &__card {
    .swiper-pagination-bullet {
      background-image: none;
      width: 25%;
      border-radius: 0;
      border-bottom: mini.rem(1) solid var(--main-color);
      opacity: 0.1;
      transition: opacity 0.3s ease 0s;
      &-active {
         opacity: 1;
      }
      @media (any-hover: hover) {
         &:hover {
            opacity: 0.5;
         }
      }
    }
  }


  .pages__button {
    display: flex;
  }
  .swiper-slide {
    width: 100% !important;
  }


  // .checkbox__text
  .checkbox__text {
    font-size: mini.rem(14);
    margin-right: auto;
    @include breakpoint.mobile {
      font-size: mini.rem(16);
    }
  }
}

.catalog {
  // .catalog__control
  &__control {
  }
}
.control {
  position: relative;
  // .control__bar
  &__bar {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-25%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 20;
    @include breakpoint.big-desktop {
      position: inherit;
      transform: none;
    }
  }
  // .control__button
  &__button {
    display: flex;
    align-items: center;
    transition: opacity 0.3s ease 0s;
    font-size: mini.rem(18);
    text-transform: uppercase;
    font-family: 'Bebas Neue Cyrillic', sans-serif;
    font-weight: 400;
    color: inherit;
    @media (any-hover: hover) {
      &:hover {
        opacity: 0.4;
      }
    }
    &:not(:first-child) {
      margin-left: mini.rem(30);
      @include breakpoint.mobile {
        margin-left: mini.rem(25);
      }
    }
    &:first-child {
      margin-right: auto;
    }
    &::before {
      @include mini.pseudo;
      margin-top: mini.rem(-4);
      width: mini.rem(24);
      height: mini.rem(24);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      margin-right: mini.rem(12);
      @include breakpoint.mobile {
        margin-right: mini.rem(10);
      }
    }
    &#button-filter {
      span {
        @include breakpoint.mobile {
          display: none;
        }
      }
    }
    &#button-filter::before {
      background-image: url(../img/svg/filter.svg);
    }
    &--active#button-filter::before {
      background-image: url(../img/svg/close.svg);
    }
    &#button-sort-price::before, &#button-sort-popularity::before {
      background-image: url(../img/svg/sorting.svg);
    }
    &--reverse::before {
      transform: rotate(180deg);
    }
    &#button-view {
      @include breakpoint.small-tablet {
        display: none;
      }
    }
    &#button-view::before {
      background-image: url(../img/svg/grid-2x2.svg);
      margin-right: 0;
      @include breakpoint.tablet {
        background-image: url(../img/svg/grid-1x1.svg);
      }
    }
    &--active#button-view::before {
      background-image: url(../img/svg/grid-4x4.svg);
      @media (max-width: mini.rem(1500)) {
        background-image: url(../img/svg/grid-3x3.svg);
      }
      @include breakpoint.tablet {
        background-image: url(../img/svg/grid-2x2.svg);
      }
    }
  }
  // .control__form
  &__form {
    display: none;
    &--active {
      display: block;
    }
  }
}

.form-control {
  position: absolute;
  //display: flex;
  flex-direction: column;
  top: mini.rem(84);
  left: 0;
  background: var(--bg-color);
  padding: mini.rem(45) mini.rem(50) mini.rem(50);
  @include breakpoint.tablet {
    top: mini.rem(50);
    padding: mini.rem(35) mini.rem(30) mini.rem(40);
  }
  @include breakpoint.small-tablet {
    margin-right: calc(var(--container-offset) * -1);
    margin-left: calc(var(--container-offset) * -1);
    width: calc(100% + (var(--container-offset) * 2));
  }
  @include breakpoint.mobile {
    top: mini.rem(45);
    padding: mini.rem(35) mini.rem(20) mini.rem(40);
  }
  // .form-control__body
  &__body {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: mini.rem(10);
    margin-right: mini.rem(-50);
    max-height: mini.rem(500);
    @include breakpoint.tablet {
      margin-right: mini.rem(-30);
    }
    @include breakpoint.mobile {
      margin-right: 0;
    }
  }
  // .form-control__fieldset
  &__fieldset {
    float: left;
    border: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    width: mini.rem(209);
    margin-bottom: mini.rem(30);
    margin-right: mini.rem(50);
    @include breakpoint.tablet {
      margin-right: mini.rem(30);
    }
    @include breakpoint.small-tablet {
      margin-right: 0;
      width: calc(100% - var(--container-offset));
    }
    @include breakpoint.mobile {
      width: 100%;
    }
  }
  // .form-control__legend
  &__legend {
    font-family: 'Bebas Neue Cyrillic', sans-serif;
    font-weight: 400;
    font-size: mini.rem(18);
    line-height: 111%;
    margin-bottom: mini.rem(15);
    @include breakpoint.tablet {
      margin-bottom: mini.rem(10);
    }
  }
  // .form-control__checkbox
  &__checkbox {
    &:not(:last-child) {
      margin-bottom: mini.rem(10);
    }
  }
  // .form-control__bottom
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  // .form-control__button
  &__button {
    min-width: 0;
    @include breakpoint.mobile {
      width: 100%;
      &:not(:last-child) {
        margin-right: mini.rem(20);
      }
    }
  }
}

.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  // .checkbox__input
  &__input {
  }
  // .checkbox__count
  &__count {
    font-weight: 500;
    font-size: mini.rem(13);
    color: rgba(0, 0, 0, 0.4);
    margin-left: mini.rem(10);
    @include breakpoint.mobile {
      font-size: mini.rem(16);
    }
  }
  // .checkbox__icon
  &__icon {
    margin-right: mini.rem(10);
  }
}