@use "../mixins/mini";
@use "../mixins/breakpoint";

.gallery {
  margin-bottom: mini.rem(80);
  @include breakpoint.small-tablet {
    margin-bottom: 0;
  }
  // .gallery__container
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  // .gallery__title
  &__title {
    position: absolute;
    margin-bottom: 0;
    .button {
      font-size: inherit;
    }
  }
  // .gallery__list
  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, minmax(mini.rem(720), 1fr));
    @include breakpoint.small-tablet {
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: repeat(1, minmax(mini.rem(500), 1fr));
    }
    @include breakpoint.mobile {
      grid-template-rows: repeat(1, minmax(mini.rem(470), 1fr));
    }
  }
  &__link {
    width: 100%;
  }
  // .gallery__item
  &__item {
    display: none;
    &:nth-child(1), &:nth-child(2) {
      display: flex;
    }
    @include breakpoint.small-tablet {
      &:nth-child(2) {
        display: none;
      }
    }
  }
  // .gallery__image
  &__image, picture {
    width: 100%;
    height: 100%;
  }
}