@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.why {
  padding-bottom: mini.rem(110);
  background-color: var(--bg-color);
  background-image: url(../img/logo-bg-light.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center top;
  overflow: visible;
  @include breakpoint.tablet {
    padding-bottom: mini.rem(60);
    background-position: center mini.rem(130);
    background-size: mini.rem(1050) mini.rem(530);
  }
  @include breakpoint.mobile {
    padding-bottom: mini.rem(50);
    background-position: center mini.rem(170);
  }
  // .why__container
  &__container {
    display: grid;
    grid-template-columns: mini.rem(420) 1fr mini.rem(420);
    grid-template-rows: auto auto;
    gap: mini.rem(100);
    @media (max-width: mini.rem(1920)) {
      grid-template-columns: 22% 1fr 22%;
      gap: 4%;
    }
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, auto);
      gap: mini.rem(50) mini.rem(80);
    }
    @include breakpoint.small-mobile {
      gap: mini.rem(50);
    }
    @include breakpoint.mobile {
      gap: mini.rem(30) mini.rem(0);
    }
  }
  // .why__picture
  &__picture {
    &:first-child {
      .why__image {
        transform: rotate(-15deg);
      }
    }
    &:last-child {
      .why__image {
        transform: rotate(15deg);
      }
    }
  }
  // .why__content
  &__content {
    padding-top: mini.rem(73);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include breakpoint.tablet {
      padding-top: mini.rem(60);
      grid-column: 1/3;
      grid-row: 1/2;
    }
    @include breakpoint.mobile {
      padding-top: mini.rem(50);
    }
    @media (max-width: mini.rem(1920)) {
      max-width: mini.rem(760);
    }
  }
  &__image {
    width: 100%;
    margin: 0 auto;
    @include breakpoint.tablet {
      width: 90%;
    }
  }
  // .why__title
  &__title {
    margin-bottom: mini.rem(26);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(23);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(18);
    }
  }
  // .why__subtitle
  &__subtitle {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: mini.rem(22);
    line-height: 127%;
    text-align: center;
    margin-bottom: mini.rem(26);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(23);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(18);
      margin-bottom: mini.rem(18);
    }
  }
  // .why__text
  &__text {
    text-align: center;
    opacity: 0.6;
    @include breakpoint.small-tablet {
      opacity: 1;
    }
    &:not(:last-child) {
      margin-bottom: mini.rem(20);
      @include breakpoint.mobile {
        margin-bottom: mini.rem(15);
      }
    }
  }
}