@use '../mixins/mini';
@use '../mixins/breakpoint';

.basket {
  display: flex;
  flex-direction: column;
  //position: relative;
  &__wrapper {

  }
  // .basket__title
  &__title {
    font-size: mini.rem(20);
    line-height: 127%;
    text-align: left;
    width: 100%;
  }
  // .basket__form
  &__form {
    display: flex;
    flex-direction: column;
  }
  // .basket__list
  &__list {
    display: flex;
    flex-direction: column;
    & + .button {
      margin-top: mini.rem(17);
    }
  }
  // .basket__item
  &__item {
    &:not(:first-child) {
        border-top: mini.rem(1) solid var(--second-color);
      @include breakpoint.mobile {
        border-top: none;
      }
    }
  }
  // .basket__total
  &__total {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: mini.rem(26) 0;
    @include breakpoint.mobile {
      justify-content: center;
    }
  }
  // .basket__subtext
  &__subtext {
    font-size: mini.rem(14);
    line-height: 170%;
    color: rgba(0, 0, 0, 0.3);
    margin-right: mini.rem(15);
  }
  // .basket__output
  &__output {
    font-size: mini.rem(26);
    line-height: 115%;
  }
  // .basket__row
  &__row {
    display: flex;
    align-items: center;
    @include breakpoint.mobile {
      flex-direction: column;
    }
  }
  // .basket__button
  &__button {
    .button__icon {
      width: mini.rem(18);
      height: mini.rem(18);
    }
    &:last-child {
      margin-left: mini.rem(20);
      @include breakpoint.mobile {
        margin-left: 0;
        margin-top: mini.rem(20);
      }
    }
  }
  #basket-button-null {
    display: none;
    width: auto;
    align-self: flex-start;
    margin-top: mini.rem(30);
    margin-left: 0;
    @include breakpoint.mobile {
      width: 100%;
    }
  }
}

.item-basket {
  display: grid;
  grid-template-columns: mini.rem(35) 1fr mini.rem(84) mini.rem(120) mini.rem(24);
  gap: 0 mini.rem(20);
  padding: mini.rem(10) 0;
  width: mini.rem(524);
  & > * {
    align-self: center;
  }
  @include breakpoint.small-tablet {
    width: 100%;
  }
  @include breakpoint.mobile {
    grid-template-columns: mini.rem(35) mini.rem(84) 1fr mini.rem(24);
    gap: mini.rem(8) mini.rem(10);
    padding: mini.rem(9) 0;
  }

  // .item-basket__link
  &__link {
    display: flex;
    align-items: center;
    @include breakpoint.mobile {
      grid-column: 2/4;
      grid-row: 1/2;
    }
    @media (any-hover: hover) {
      &:hover {
        .item-basket__title {
          color: rgba(0, 0, 0, 0.4);
        }
      }
    }
  }
  // .item-basket__image
  &__image {
    width: mini.rem(35);
    height: mini.rem(40);
    @include breakpoint.mobile {
      grid-column: 1/2;
      grid-row: 1/3;
    }
  }
  // .item-basket__title
  &__title {
    margin: 0;
    font-weight: 500;
    font-size: mini.rem(14);
    line-height: 143%;
    color: var(--main-color);
    transition: color 0.3s ease 0s;
    @include breakpoint.mobile {
      font-weight: 400;
      font-size: mini.rem(14);
      grid-column: 2/4;
      grid-row: 1/2;
    }
  }
  // .item-basket__quantity
  &__quantity {
    @include breakpoint.mobile {
      justify-self: start;
      width: mini.rem(84);
      grid-column: 2/3;
      grid-row: 2/3;
    }
  }
  // .item-basket__output
  &__output {
    font-size: mini.rem(16);
    line-height: 150%;
    margin-left: mini.rem(20);
    @include breakpoint.small-tablet {
      margin-left: mini.rem(10);
    }
    @include breakpoint.mobile {
      margin-left: 0;
      justify-self: end;
      grid-column: 3/5;
      grid-row: 2/3;
    }
  }
  // .item-basket__remove
  &__remove {
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    width: mini.rem(24);
    height: mini.rem(24);
    background-repeat: no-repeat;
    background-size: mini.rem(16) mini.rem(16);
    background-position: center;
    background-image: url(../img/svg/remove.svg);
    @include breakpoint.mobile {
      grid-column: 4/5;
      grid-row: 1/2;
      justify-self: end;
    }
    @media (any-hover: hover) {
      &:hover {
        background-image: url(../img/svg/remove-accent.svg);
      }
    }
  }
}

.quantity {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: mini.rem(120);
  &__button {
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    width: mini.rem(20);
    height: mini.rem(20);
    background-repeat: no-repeat;
    background-size: mini.rem(14) mini.rem(14);
    background-position: center;
    @media (any-hover: hover) {
      &:hover {
        &:first-child {
          background-image: url(../img/svg/icon-minus-accent.svg);
        }
        &:last-child {
          background-image: url(../img/svg/icon-plus-accent.svg);
        }
      }
    }
    &:first-child {
      background-image: url(../img/svg/icon-minus.svg);
    }
    &:last-child {
      background-image: url(../img/svg/icon-plus.svg);
    }
  }
  &__count {
    display: inline-block;
    font-weight: 600;
    font-size: mini.rem(16);
    line-height: 150%;
    text-align: center;
    margin: 0 mini.rem(10);
  }
}