@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.theses {
  padding: mini.rem(73) 0 mini.rem(91);
  @include breakpoint.small-tablet {
    padding: mini.rem(60) 0;
  }
  @include breakpoint.mobile {
    padding: mini.rem(50) 0 mini.rem(64);
  }
  
  &__container {
    display: flex;
    flex-direction: column;
  }
  // .theses__title
  &__title {
    max-width: mini.rem(1084);
    margin-bottom: mini.rem(25);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(20);
    }
  }
  // .theses__subtitle
  &__subtitle {
    text-align: center;
    margin-bottom: mini.rem(52);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(46);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(23);
    }
  }
  // .theses__list
  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    @media (max-width: mini.rem(1920)) {
      flex-wrap: wrap;
      gap: mini.rem(30);
    }
    @include breakpoint.tablet {
      flex-direction: column;
    }
  }
  // .theses__item
  &__item {
    display: flex;
    flex-direction: column;
    margin-right: mini.rem(30);
    width: 20%;
    @media (max-width: mini.rem(1920)) {
      margin-right: 0;
      width: auto;
      max-width: mini.rem(336);
    }
    @include breakpoint.tablet {
      max-width: 100%;
      width: 100%;
      flex-direction: row;
      justify-content: flex-start;
    }
  }
  // .theses__count
  &__count {
    margin-bottom: mini.rem(50);
    margin-left: mini.rem(40);
    @include breakpoint.tablet {
      margin-left: 0;
      margin-bottom: 0;
      margin-right: mini.rem(25);
      flex: 0 0 mini.rem(80);
    }
    @include breakpoint.mobile {
      margin-right: mini.rem(15);
    }
  }
  // .theses__image
  &__image {
    max-height: mini.rem(200);
    height: 100%;
    object-fit: contain;
    @include breakpoint.tablet {
      max-height: mini.rem(100);
      min-width: mini.rem(80);
    }
  }
  // .theses__content
  &__content {
    @include breakpoint.tablet {
      width: 100%;
    }
  }
  // .theses__caption
  &__caption {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 127%;
    margin-bottom: mini.rem(11);
    @include breakpoint.small-tablet {
      font-size: mini.rem(30);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(24);
    }
  }
  // .theses__text
  &__text {
    opacity: 0.6;
  }
}