@use '../mixins/mini';
@use '../mixins/breakpoint';

.boutiques {
  // .boutiques__container
  &__container {

  }

  // .boutiques__breadcrumbs
  &__breadcrumbs {
  }

  // .boutiques__title
  &__title {
  }

  // .boutiques__list
  &__list {
    margin-bottom: mini.rem(60);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(50);
    }
  }

  // .boutiques__item
  &__item {
    overflow: hidden;
    position: relative;
    display: flex;
    background: var(--bg-color);
    @include breakpoint.tablet {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: mini.rem(40);
       @include breakpoint.small-tablet {
          margin-bottom: mini.rem(30);
       }
       @include breakpoint.mobile {
          margin-bottom: mini.rem(20);
       }
    }
  }

  // .boutiques__box
  &__box {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: mini.rem(60) mini.rem(60) mini.rem(80);
    @include breakpoint.desktop {
      padding: mini.rem(60) mini.rem(60) mini.rem(80) mini.rem(80);
    }
     @include breakpoint.tablet {
        padding: mini.rem(40) mini.rem(60) mini.rem(45);
     }
     @include breakpoint.tablet {
        padding: mini.rem(32) mini.rem(20) mini.rem(35);
     }
  }

  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: mini.rem(30);
    @include breakpoint.tablet {
      flex-direction: column;
      align-items: stretch;
      text-align: center;
    }
  }

  // .boutiques__content
  &__content {
    margin-right: mini.rem(30);
    max-width: mini.rem(342);
    margin-bottom: mini.rem(30);

     @include breakpoint.tablet {
        margin-right: 0;
        max-width: 100%;
     }

    .boutiques__title {
      text-align: left;
      font-weight: 400;
      font-size: mini.rem(30);
      line-height: 127%;
      margin-bottom: mini.rem(29);
       @include breakpoint.tablet {
          text-align: center;
       }
      @include breakpoint.small-tablet {
        margin-bottom: mini.rem(19);
      }
      @include breakpoint.mobile {
        font-size: mini.rem(24);
        margin-bottom: mini.rem(16);
      }
    }
  }

  // .boutiques__text
  &__text {
    @include breakpoint.small-tablet {
      font-size: mini.rem(16);
    }
  }

  // .boutiques__button
  &__button {
    align-self: flex-start;
    margin-top: auto;
     @include breakpoint.tablet {
       align-self: stretch;
     }
  }

  // .boutiques__contact
  &__contact {
  }

  &__wrap {
    width: 46%;
    position: relative;
    right: 0;
    padding-top: 21%;
    @include breakpoint.tablet {
      width: 100%;
      padding-top: 0;
    }
  }

  // .boutiques__gallery
  &__gallery {
    width: 100%;
    height: 100%;
    //max-width: 46%;
    position: absolute;
    top: 0;
    right: 0;
    display: grid;
    grid-template-columns: minmax(mini.rem(200), 33%) 1fr;
    grid-template-rows: 50% 50%;
     @include breakpoint.tablet {
        position: inherit;
        display: block;
     }
  }

  // .boutiques__slide
  &__slide {
    display: flex;

    &:nth-child(2) {
      grid-column: 2/3;
      grid-row: 1/3;
    }
  }

  // .boutiques__play
  &__play, picture, &__image {
    height: 100%;
    width: 100%;
  }

  // .boutiques__image
  &__image {
  }

  // .boutiques__map
  &__map {
  }

  // .boutiques__pages
  &__pages {
  }

  .play__button {
    width: mini.rem(100);
    height: mini.rem(100);
    background-size: mini.rem(40) mini.rem(40);
  }
  .swiper {
    height: mini.rem(470);
    @include breakpoint.mobile {
      height: mini.rem(420);
    }
  }
}

.contact-boutiques {
  max-width: mini.rem(327);
  width: 100%;
   @include breakpoint.tablet {
     max-width: 100%;
   }
  // .contact-boutiques__item
  &__item {
    background: var(--light-color);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-weight: 500;
    font-size: mini.rem(16);
    line-height: 187%;
    color: var(--main-color);
    padding: mini.rem(13) mini.rem(30) mini.rem(12);
    min-height: mini.rem(55);
    width: 100%;
    white-space: nowrap;
    &--phone {
      font-size: mini.rem(18);
    }
     @include breakpoint.tablet {
       justify-content: center;
     }

    &:not(:last-child) {
      margin-bottom: mini.rem(10);
    }
  }

  &__icon {
    width: mini.rem(18);
    height: mini.rem(18);
    margin-right: mini.rem(10);
  }

  // .contact-boutiques__link
  &__link {
    color: inherit;
  }
}

.map {
  width: 100%;
  height: mini.rem(730);
  margin-bottom: mini.rem(100);
  @include breakpoint.mobile {
    height: mini.rem(550);
  }
  @include breakpoint.tablet {
    margin-bottom: mini.rem(60);
  }
  @include breakpoint.mobile {
    margin-bottom: mini.rem(50);
  }

  &__marker {
    transform: translate(-50%, calc(-100% - mini.rem(5))) rotate(-135deg);
    width: mini.rem(40);
    height: mini.rem(40);
    background: var(--main-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 100% 100% 100%;
  }
  img {
    transform: rotate(135deg);
    width: mini.rem(17);
    height: mini.rem(22.5);
  }
  .circle-content {
    transform: translate(-50%, -50%);
    width: mini.rem(50);
    height: mini.rem(50);
    background-image: url(../img/pages.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .circle-text {
    margin-top: mini.rem(8);
    color: var(--light-color);
  }
}
