/* stylelint-disable */
@use "./mini";

@mixin desktop {
  @media (min-width: (mini.rem(1025))) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (mini.rem(1440))) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (mini.rem(960))) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (mini.rem(768))) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (mini.rem(620))) {
    @content;
  }
}


@mixin small-mobile {
  @media (max-width: (mini.rem(375))) {
    @content;
  }
}