@use "../mixins/mini";
@use "../mixins/breakpoint";

.bestsellers {
  // .bestsellers__container
  &__container {
  }
  // .bestsellers__swiper
  &__swiper.swiper--main {

    @include breakpoint.tablet {
      height: mini.rem(540);
    }
    @include breakpoint.mobile {
      height: mini.rem(520);
    }
  }
  .swiper__title {
    @include breakpoint.tablet {
      max-width: mini.rem(300);
    }
    @include breakpoint.small-tablet {
      max-width: mini.rem(200);
    }
  }
  .swiper-pagination {
    opacity: 0;
    pointer-events: none;
  }
}