@use '../mixins/mini';
@use '../mixins/breakpoint';

.showroom {
   margin-bottom: mini.rem(100);
   @include breakpoint.mobile {
      margin-bottom: mini.rem(80);
   }
   // .showroom__container
   &__container {
      @include breakpoint.desktop {
         display: grid;
         grid-template-columns: 1fr mini.rem(267) mini.rem(267) 1fr;
         grid-template-rows: 1fr;
         gap: mini.rem(40);
      }
   }
   // .showroom__content
   &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: mini.rem(55);
      @include breakpoint.desktop {
         grid-column: 2/4;
         margin-bottom: 0;
      }
   }
   // .showroom__title
   &__title {
      margin-bottom: mini.rem(19);
      @include breakpoint.small-tablet {
         margin-bottom: mini.rem(24);
      }
      @include breakpoint.mobile {
         margin-bottom: mini.rem(18);
      }
   }
   // .showroom__text
   &__text {
      text-align: center;
      margin-bottom: mini.rem(40);
      @include breakpoint.mobile {
         margin-bottom: mini.rem(35);
      }
   }
   // .showroom__button
   &__button {
      min-width: mini.rem(267);
   }
   .swiper {
      height: mini.rem(470);
      @include breakpoint.desktop {
         height: auto;
      }
   }
   // .showroom__slide
   &__slide {
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint.desktop {
         &:nth-child(2) {
            grid-column: 1/2;
            grid-row: 1/3;
         }
         &:nth-child(3) {
            grid-column: 4/5;
            grid-row: 1/2;
         }
         &:nth-child(6) {
            grid-column: 4/5;
            grid-row: 2/3;
         }
      }
   }
   // .showroom__image
   &__image,
   picture {
      width: 100%;
      height: 100%;
   }
}
