@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.hero-about {
  // .hero-about__container
  &__container {
  }
  // .hero-about__swiper
  &__swiper {
    .swiper-slide {
      &::after {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 18.5%, rgba(0, 0, 0, 0) 49.5%, rgba(0, 0, 0, 0) 84%, rgba(0, 0, 0, 0.6) 100%);
      }
    }
  }
  .swiper__logo-image {
    width: mini.rem(60);
    @include breakpoint.mobile {
      width: mini.rem(38);
    }
   }
  .swiper__title {
    font-size: mini.rem(60);
    margin-bottom: mini.rem(22);
    color: var(--light-color);
    @include breakpoint.small-tablet {
      font-size: mini.rem(60);
      margin-bottom: mini.rem(22);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(40);
      margin-bottom: mini.rem(10);
    }
  }
  .swiper__subtitle {
    color: var(--light-color);
  }
}