/* stylelint-disable */
@use "../mixins/mini";

@mixin checkbox {
  position: relative;
  &--disabled, &:has(.checkbox__input:disabled) {
    pointer-events: none;
    .checkbox__box::before {
      background: #E8E8E8;
    }
    .checkbox__input {
      &:checked + .checkbox__box::after {
          opacity: 0.2;
      }
    }
    .checkbox__text {
      color: rgba(0, 0, 0, 0.4);
    }
  }
  @media (any-hover: hover) {
    &:hover {
      .checkbox__box::before {
        border: mini.rem(1) solid var(--main-color);
      }
    }
  }
  &__input {
    appearance: none;
    position: absolute;
    border: none !important;
    &:checked + .checkbox__box::after {
      opacity: 1;
    }
    &:focus + .checkbox__box::before {
      //outline: mini.rem(1) solid var(--accent-text-color);
    }
    //&:disabled + .checkbox__box {
    //  pointer-events: none;
    //  &::before {
    //    background: #E8E8E8;
    //  }
    //}
  }

  &__box {
    display: flex;
    //padding-left: mini.rem(25);
    cursor: pointer;
    //display: flex;

    &::before {
      content: "";
      display: block;
      position: relative;
      left: 0;
      top: 0;
      border: mini.rem(1) solid transparent;
      width: mini.rem(24);
      height: mini.rem(24);
      flex-shrink: 0;
      box-sizing: border-box;
      background: var(--light-color);
      transition: border 0.3s ease 0s;
      //border-radius: mini.rem(5);
      //margin-right: mini.rem(7);
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: mini.rem(24);
      height: mini.rem(24);
      box-sizing: border-box;
      background-image: url(../img/svg/check.svg);
      background-position: center;
      background-size: mini.rem(14) mini.rem(15);
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
}
