@use "../mixins/mini";
@use "../mixins/breakpoint";

.confirmation {
  padding: mini.rem(80) 0 mini.rem(105);
  @include breakpoint.small-tablet {
    padding: mini.rem(70) 0 mini.rem(100);
  }
  @include breakpoint.mobile {
    padding: mini.rem(40) 0 mini.rem(80);
  }
  // .confirmation__container
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  // .confirmation__icon
  &__icon {
    width: mini.rem(100);
    height: mini.rem(100);
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-color);
    margin-bottom: mini.rem(36);
    svg {
      width: mini.rem(60);
      height: mini.rem(60);
    }
    @include breakpoint.mobile {
      width: mini.rem(60);
      height: mini.rem(60);
      margin-bottom: mini.rem(20);
      svg {
        width: mini.rem(36);
        height: mini.rem(36);
      }
    }
  }
  // .confirmation__title
  &__title {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: 50px;
    line-height: 120%;
    text-align: center;
    margin-bottom: mini.rem(34);
    @include breakpoint.mobile {
      font-size: mini.rem(34);
      margin-bottom: mini.rem(31);
    }
  }
  // .confirmation__list
  &__list {
    border: mini.rem(1) solid rgba(0, 0, 0, 0.08);
    padding: mini.rem(25) mini.rem(35);
    margin-bottom: mini.rem(37);
    width: 100%;
    max-width: mini.rem(574);
    @include breakpoint.mobile {
      padding: mini.rem(15) mini.rem(15);
      margin-bottom: mini.rem(22);
    }
  }
  // .confirmation__item
  &__item {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    &:not(:last-child) {
      margin-bottom: mini.rem(20);
      @include breakpoint.mobile {
        margin-bottom: mini.rem(15);
      }
    }
    &::before {
      @include mini.pseudo;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translateY(mini.rem(-5));
      border-bottom: mini.rem(1) dashed rgba(0, 0, 0, 0.08);
      width: 100%;
    }
  }
  // .confirmation__heading
  &__heading {
    font-size: mini.rem(18);
    color: rgba(0, 0, 0, 0.5);
    display: inline-block;
    background: var(--light-color);
    padding: 0 mini.rem(5);
    z-index: 1;
    @include breakpoint.mobile {
      font-size: mini.rem(16);
    }
  }
  // .confirmation__details
  &__details {
    font-weight: 500;
    font-size: mini.rem(18);
    text-align: right;
    display: inline-block;
    background: var(--light-color);
    padding: 0 mini.rem(5);
    z-index: 1;
    @include breakpoint.mobile {
      font-size: mini.rem(16);
    }
  }
  // .confirmation__text
  &__text {
    font-size: mini.rem(13);
    line-height: 138%;
    text-align: center;
    margin-bottom: mini.rem(27);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(19);
    }
  }
  // .confirmation__button
  &__button {
    width: 100%;
    max-width: mini.rem(360);
    &:not(:last-child) {
      margin-bottom: mini.rem(40);
      @include breakpoint.mobile {
        margin-bottom: mini.rem(26);
      }
    }
  }
  // .confirmation__info
  &__info {
    text-align: center;
    margin-bottom: mini.rem(25);
  }
}