@use "./mixins/mini";
@use "./mixins/breakpoint";

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  text-size-adjust: 100%;
  background: var(--light-color);
  -webkit-tap-highlight-color: transparent;
}

.page__body {
  margin: 0;
  min-width: mini.rem(375);
  min-height: 100%;
  font-size: mini.rem(18);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  display: block;
}

a {
  text-decoration: none;
}

.site-container {
  @include mini.footerToBottom;
}

.main {
  margin-top: var(--header-height);
  .swiper--main {
    width: 100%;
    height: calc(900 / 1920 * 100vw);
    min-height: mini.rem(900);
    @include breakpoint.tablet {
      height: mini.rem(780);
      min-height: 0;
    }
    @include breakpoint.mobile {
      height: mini.rem(540);
    }
  }
  &--top {
    margin-top: 0;
    .breadcrumbs {
      position: absolute;
      z-index: 50;
      margin-top: calc(var(--header-height) - mini.rem(35));
      @include breakpoint.tablet {
        margin-top: calc(var(--header-height) - mini.rem(25));
      }
      @include breakpoint.mobile {
        margin-top: calc(var(--header-height));
      }
    }
  }
}
.header--light {
  & + .main--top {
    .breadcrumbs__item {
      color: var(--light-color);
    }
    .breadcrumbs__item:first-child .breadcrumbs__link {
      background-image: url(../img/svg/home-line-light.svg);
    }
  }
}
.header--light:not(.header--slim) {
  & + .main--top {
    .breadcrumbs {
      z-index: 100;
    }
  }
}

//header,
section,
footer {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &::-webkit-search-decoration {
    display: none;
  }
  &::-webkit-search-cancel-button {
    display: none;
  }
  &::-webkit-search-results-button {
    display: none;
  }
  &::-webkit-search-results-decoration {
    display: none;
  }
  &::-webkit-calendar-picker-indicator {
    display: none;
  }
  &::-moz-calendar-picker-indicator {
    display: none;
  }
  &::-ms-clear {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: mini.rem(-1);
  border: 0;
  padding: 0;
  width: mini.rem(1);
  height: mini.rem(1);
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);

  &--holder {
    max-width: var(--container-width);
  }
  &--wide {
    @include breakpoint.desktop {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &--pull {
    @include breakpoint.tablet {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page-ios .disable-scroll {
  position: relative;
}
