@use '../mixins/mini';
@use '../mixins/breakpoint';

.cart {
  height: 100%;
  margin-bottom: mini.rem(100);
  @include breakpoint.mobile {
    margin-bottom: mini.rem(80);
  }
  // .cart__container
  &__container {
    height: 100%;
  }
  // .cart__breadcrumbs
  &__breadcrumbs {
  }
  &__wrap {
    display: flex;
    flex-direction: column;
    height: 100%;
    &--center {
      justify-content: center;
      transform: translateY(mini.rem(-66));
    }
  }
  // .cart__title
  &__title {
  }
  // .cart__form
  &__form {
    display: flex;
    flex-direction: column;
  }
  // .cart__list
  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: mini.rem(36);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(30);
    }
  }
  // .cart__item
  &__item {
    &:not(:last-child) {
      margin-bottom: mini.rem(40);
      @include breakpoint.small-tablet {
        margin-bottom: mini.rem(30);
      }
      @include breakpoint.mobile {
        margin-bottom: mini.rem(20);
      }
    }
  }
  // .cart__bottom
  &__bottom {
    align-self: flex-end;
    min-width: mini.rem(340);
    @include breakpoint.mobile {
      min-width: 0;
      width: 100%;
    }
  }
  // .cart__total
  &__total {
    display: flex;
    flex-direction: column;
    margin-bottom: mini.rem(34);
  }
  // .cart__total-item
  &__total-item {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    &::before {
      @include mini.pseudo;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translateY(mini.rem(-5));
      border-bottom: mini.rem(1) dashed rgba(0, 0, 0, 0.08);
      width: 100%;
    }
    &:not(:last-child) {
      margin-bottom: mini.rem(10);
    }
  }
  // .cart__caption
  &__caption {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(18);
    line-height: 111%;
    display: inline-block;
    background: var(--light-color);
    padding: 0 mini.rem(5);
    margin: 0 mini.rem(-5);
    z-index: 1;
  }
  // .cart__output
  &__output {
    font-size: mini.rem(26);
    line-height: 115%;
    text-align: right;
    display: inline-block;
    background: var(--light-color);
    padding: 0 mini.rem(5);
    margin: 0 mini.rem(-5);
    z-index: 1;
    &#cart-price-total {
      font-size: mini.rem(40);
      line-height: 100%;
    }
  }
  // .cart__button
  &__button {
    &#cart-button-null {
      min-width: mini.rem(266);
      align-self: center;
    }
  }
}

.item-cart {
  position: relative;
  background: var(--bg-color);
  display: grid;
  grid-template-columns: 8fr 2fr 1.5fr 2fr;
  gap: mini.rem(34);
  padding: mini.rem(50) mini.rem(110) mini.rem(50) mini.rem(60);
  @include breakpoint.big-desktop {
    grid-template-columns: 6fr 2fr 2fr;
    padding: mini.rem(40) mini.rem(60) mini.rem(40) mini.rem(40);
  }
  @include breakpoint.tablet {
    grid-template-columns: 3.5fr 3fr 2fr;
  }
  @include breakpoint.small-tablet {
    padding: mini.rem(30) mini.rem(60) mini.rem(30) mini.rem(30);
  }
  @include breakpoint.mobile {
    grid-template-columns: 1fr;
    padding: mini.rem(20) mini.rem(20) mini.rem(30);
    gap: mini.rem(20);
  }
  // .item-cart__col
  &__col {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    gap: mini.rem(25) 0;
    @include breakpoint.mobile {
      width: 100%;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto;
      position: relative;
      & > * {
        align-self: end;
      }
      &::before {
        @include mini.pseudo;
        position: absolute;
        bottom: 0;
        left: 0;
        transform: translateY(mini.rem(-5));
        border-bottom: mini.rem(1) dashed rgba(0, 0, 0, 0.08);
        width: 100%;
      }
    }
    &:nth-child(2) {
      @include breakpoint.mobile {
        & > * {
          align-self: start;
        }
        &::before {
          bottom: auto;
          top: mini.rem(16);
        }
      }
    }
    &:nth-child(3) {
      @include breakpoint.big-desktop {
        justify-self: center;
        .item-cart__heading {
          opacity: 0;
          visibility: hidden;
        }
      }
      @include breakpoint.small-tablet {
        justify-self: start;
      }
      @include breakpoint.mobile {
        .item-cart__heading {
          opacity: 1;
          visibility: visible;
        }
      }
    }
    &--head {
      grid-template-columns: 1fr 2fr;
      @include breakpoint.big-desktop {
        grid-template-columns: minmax(mini.rem(165), 1fr) 4fr;
        gap: mini.rem(25) mini.rem(40);
        grid-column: 1/4;
      }
      @include breakpoint.small-tablet {
        grid-template-columns: minmax(mini.rem(85), 1fr) 3fr;
        gap: mini.rem(15) mini.rem(30);
      }
      @include breakpoint.mobile {
        grid-template-columns: mini.rem(85) 1fr;
        grid-column: 1/2;
        gap: mini.rem(15);
        .item-cart__heading {
          align-self: center;
        }
      }
    }
    &:not(:first-child) {
      margin-top: mini.rem(10);
      @include breakpoint.big-desktop {
        margin-top: 0;
      }
    }
  }
  // .item-cart__picture
  &__picture {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: mini.rem(40);
    background: var(--light-color);
    grid-row: 1/3;
    margin: mini.rem(-50) mini.rem(60) mini.rem(-50) mini.rem(-60);
    @include breakpoint.big-desktop {
      padding-right: 0;
      margin: 0;
      background: transparent;
    }
    @include breakpoint.mobile {
      grid-row: 1/2;
    }
  }
  // .item-cart__image
  &__image {
    height: auto;
    width: auto;
    @include breakpoint.mobile {
      height: mini.rem(100);
    }
  }
  // .item-cart__heading
  &__heading {
    margin: 0;
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(24);
    color: var(--main-color);
    & ~:last-child {
      margin-bottom: auto;
    }
    @include breakpoint.mobile {
      font-size: mini.rem(16);

      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
      justify-self: start;
    }
  }
  // .item-cart__title
  &__title {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: mini.rem(30);
    line-height: 127%;
    @include breakpoint.mobile {
      font-size: mini.rem(24);
    }
  }
  // .item-cart__list
  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    @include breakpoint.mobile {
      grid-column: 1/3;
    }
  }
  // .item-cart__item
  &__item {
    position: relative;
    display: flex;
    align-items: flex-end;
    &:not(:last-child) {
      margin-bottom: mini.rem(25);
    }
    @include breakpoint.mobile {
      justify-content: space-between;
      &:not(:last-child) {
        margin-bottom: mini.rem(15);
      }
      &::before {
        @include mini.pseudo;
        position: absolute;
        bottom: 0;
        left: 0;
        transform: translateY(mini.rem(-5));
        border-bottom: mini.rem(1) dashed rgba(0, 0, 0, 0.08);
        width: 100%;
      }
    }
  }
  // .item-cart__caption
  &__caption {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-weight: 400;
    font-size: mini.rem(18);
    line-height: 111%;
    margin-right: mini.rem(15);
    @include breakpoint.mobile {
      font-size: mini.rem(16);
      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
    }
  }
  // .item-cart__text
  &__text {
    font-weight: 400;
    font-size: mini.rem(22);
    @include breakpoint.mobile {
      font-size: mini.rem(16);
      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
    }
  }
  // .item-cart__value
  &__value {
    flex-direction: column;
    align-items: flex-start;
    @include breakpoint.big-desktop {
      flex-direction: row;
    }
    @include breakpoint.mobile {
      display: grid;
      grid-template-columns: auto 1fr;
      gap: 0 mini.rem(20);
      justify-self: end;
      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
      > * {
        align-self: center ;
        justify-self: end;
      }
    }
    .value__discount {
      margin-bottom: mini.rem(10);
      @include breakpoint.big-desktop {
        margin-right: mini.rem(25);
      }
      @include breakpoint.mobile {
        margin-bottom: 0;
        margin-right: 0;
      }
    }
    .value__old {
      margin-bottom: mini.rem(6);
      @include breakpoint.big-desktop {
        margin-right: mini.rem(20);
      }
      @include breakpoint.mobile {
        margin-bottom: 0;
        margin-right: 0;
      }
    }
    .value__price {
      @include breakpoint.mobile {
        grid-column: 2/3;
      }
    }
  }
  // .item-cart__quantity
  &__quantity {

    @include breakpoint.mobile {
      justify-self: end;
      min-width: mini.rem(130);
      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
    }
  }
  .quantity__button {
    background-size: mini.rem(18) mini.rem(18);
  }
  .quantity__count {
    font-weight: 400;
    font-size: mini.rem(22);
  }
  // .item-cart__total
  &__total {
    font-size: mini.rem(26);
    line-height: 115%;
    white-space: nowrap;
    @include breakpoint.mobile {
      font-size: mini.rem(20);
      justify-self: end;
      font-weight: 500;
      padding: 0 mini.rem(5);
      margin: 0 mini.rem(-5);
      background: var(--bg-color);
      z-index: 1;
    }
  }
  // .item-cart__remove
  &__remove {
    position: absolute;
    top: mini.rem(115);
    right: mini.rem(80);
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    width: mini.rem(24);
    height: mini.rem(24);
    background-repeat: no-repeat;
    background-size: mini.rem(20) mini.rem(20);
    background-position: center;
    background-image: url(../img/svg/remove.svg);
    @include breakpoint.big-desktop {
      top: mini.rem(20);
      right: mini.rem(20);
    }
    @include breakpoint.mobile {
      top: mini.rem(12);
      right: mini.rem(12);
    }
    @media (any-hover: hover) {
      &:hover {
        background-image: url(../img/svg/remove-accent.svg);
      }
    }
  }
}


