@use "../mixins/mini";
@use "../mixins/breakpoint";

.footer {
  background: var(--bg-color);
  // .footer__container
  &__container {
  }
  // .footer__top
  &__top {
    padding: mini.rem(75) 0 mini.rem(85);
    display: grid;
    border-bottom: mini.rem(1) solid rgba(0, 0, 0, 0.08);
    @include breakpoint.tablet {
      padding: mini.rem(65) 0 mini.rem(55);
    }
    @include breakpoint.small-tablet {
      padding: mini.rem(50) 0 mini.rem(40);
    }
    .container {
      display: grid;
      grid-template-columns: 83.5% 1fr;
      gap: mini.rem(15);
      > * {
        align-self: start;
      }
      @include breakpoint.tablet {
        grid-template-columns: 1fr 20%;
      }
      @include breakpoint.small-tablet {
        grid-template-columns: 1fr;
        gap: mini.rem(30);
      }
    }
  }
  // .footer__nav
  &__nav {
    display: grid;
    grid-template-columns: 36% 1fr;
    gap: mini.rem(15);
    > * {
      align-self: start;
    }
    @include breakpoint.tablet {
      grid-template-columns: 30% 1fr;
    }
    @include breakpoint.small-tablet {
      grid-template-columns: repeat(2, auto);
      gap: mini.rem(30);
    }
  }
  // .footer__col
  &__col {
  }
  // .footer__title
  &__title {
    margin: 0;
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-size: mini.rem(18);
    line-height: 111%;
    color: var(--main-color);
    text-transform: uppercase;
    margin-bottom: mini.rem(20);
  }
  // .footer__list
  &__list {
    flex-direction: column;
    display: flex;
    flex-wrap: wrap;
    max-height: mini.rem(190);
    align-content: start;
    @include breakpoint.small-tablet {
      align-content: stretch;
      gap: 0 mini.rem(30);
    }
  }
  // .footer__item
  &__item {
    min-width: 39%;
    @include breakpoint.tablet {
      min-width: 50%;
    }
    @include breakpoint.small-tablet {
      min-width: 0;
      max-width: 50%;
    }
  }
  // .footer__link
  &__link {
    display: inline-block;
    font-size: mini.rem(16);
    padding: mini.rem(7) 0;
    color: #000;
    transition: color 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        color: var(--accent-color);
      }
    }
  }
  // .footer__contact
  &__contact {
    .footer__list {
      flex-direction: row;
    }
  }
  // .footer__link--tel
  &__link--tel {
    font-weight: 600;
  }
  // .footer__link--mail
  &__link--mail {
    font-size: mini.rem(16);
    color: rgba(0, 0, 0, 0.50);
  }
  // .footer__bottom
  &__bottom {
    .container {
      display: grid;
      grid-template-columns: 11% 18% 1fr 32% mini.rem(193);
      gap: mini.rem(15);
      > * {
        align-self: center;
      }
      > *:not(.footer__description) {
        grid-row: 1/3;
        @include breakpoint.tablet {
          grid-row: auto;
        }
      }
      @include breakpoint.tablet {
        margin: mini.rem(50) 0 mini.rem(40);
        grid-template-columns: 24% 28% 1fr;
        gap: mini.rem(40) mini.rem(15);
      }
      @include breakpoint.small-tablet {
        margin: mini.rem(40) 0;
        grid-template-columns: repeat(1, 1fr);
        gap: mini.rem(6);
        > * {
          justify-self: center;
        }
      }
    }
    //padding: mini.rem(45) 0;
  }
  // .footer__logo
  &__logo {
    padding: mini.rem(45) mini.rem(40) mini.rem(45) 0;
    border-right: mini.rem(1) solid rgba(0, 0, 0, 0.08);
    @include breakpoint.tablet {
      padding: 0;
      border: 0;
    }
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(14);
    }
    .logo__image {
      width: mini.rem(95);
    }
  }
  // .footer__slogan
  &__slogan {
    font-weight: 500;
    font-size: mini.rem(14);
    line-height: 143%;
    text-align: center;
    color: var(--main-color);
    transition: color 0.3s ease 0s;
    @include breakpoint.tablet {
      text-align: left;
      max-width: mini.rem(120);
    }
    @include breakpoint.small-tablet {
      max-width: 100%;
      margin-bottom: mini.rem(14);
    }
    @media (any-hover: hover) {
      &:hover {
        color: var(--accent-color);
      }
    }
  }
  // .footer__social
  &__social {
    @include breakpoint.tablet {
      order: 4;
    }
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(14);
    }
  }
  // .footer__form
  &__form {
    @include breakpoint.tablet {
      justify-self: end;
    }
    @include breakpoint.small-tablet {
      order: -1;
      justify-self: center;
      margin-bottom: mini.rem(34);
    }
  }
  // .footer__description
  &__description {
    font-size: mini.rem(13);
    line-height: 185%;
    text-decoration: underline;
    color: rgba(0, 0, 0, 0.50);
    transition: color 0.3s ease 0s;
    grid-column: 5/6;
    @include breakpoint.tablet {
      grid-column: auto;
      order: 6;
      font-size: mini.rem(12);
    }
    &:last-child {
      align-self: start;
      @include breakpoint.tablet {
        order: 5;
      }
    }
    &:not(:last-child) {
      align-self: end;
      @include breakpoint.tablet {
        justify-self: end;
        align-self: center;
      }
      @include breakpoint.small-tablet {
        justify-self: center;
      }
    }
    @media (any-hover: hover) {
      &:hover {
        color: var(--accent-color);
      }
    }
  }
  .form {
    @include breakpoint.tablet {
      //min-width: mini.rem(344);
      width: 100%;
      &__label {
        margin-left: auto;
      }
    }
    @include breakpoint.small-tablet {
      &__label {
        margin-right: auto;
      }
    }
  }
}

.social {
  // .social__list
  &__list {
    display: flex;
    align-items: center;
  }
  // .social__item
  &__item {
    &:not(:last-child) {
      margin-right: mini.rem(15);
    }
  }
  // .social__link
  &__link {
    @media (any-hover: hover) {
      &:hover {
        .social__image {
          fill: var(--accent-color);
        }
      }
    }
  }
  // .social__image
  &__image {
    width: mini.rem(14);
    height: mini.rem(14);
    transition: fill 0.3s ease 0s;
    @include breakpoint.tablet {
      width: mini.rem(20);
      height: mini.rem(20);
    }
  }
}