/* stylelint-disable */
@use "../mixins/breakpoint";
@use "../mixins/mini";

@mixin burger {
  //--burger-width: 83px;
  //--burger-height: calc(14 / 16 * 1rem);
  --burger-line-height: calc(2 / 16 * 1rem);
  position: relative;
  border: none;
  padding: 0;
  //width: var(--burger-width);
  //height: var(--burger-height);
  color: var(--light-line-color);
  background: transparent;
  cursor: pointer;
  min-height: mini.rem(24);
  display: flex;
  align-items: flex-end;
  @include breakpoint.mobile {
    width: mini.rem(24);
    height: mini.rem(24);
  }
  &__text {
    font-size: mini.rem(18);
    padding-left: mini.rem(40);
    font-family: "Bebas Neue Cyrillic", sans-serif;
    @include breakpoint.mobile {
      display: none;
    }
  }
  @media (any-hover: hover) {
    &:hover:not(.burger--active) {
      //color: var(--accent-text-color);
      //&::before,
      //&::after {
      //  transform: translateX(calc(-50%));
      //}
      .burger__line {
        width: mini.rem(24);
      }
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: mini.rem(24);
    border-radius: mini.rem(12);
    height: var(--burger-line-height);
    background: currentColor;
    //transform: translateX(-50%);
    //transform: translateX(calc(-50% + mini.rem(-10)));
    transition: all 0.3s ease-in-out;
    @include breakpoint.small-tablet {
      //transform: translateX(mini.rem(-7));
    }
  }

  &::before {
    top: mini.rem(4);
  }

  &::after {
    top: calc(100% -  mini.rem(6));
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: mini.rem(14);
    border-radius: mini.rem(12);
    height: var(--burger-line-height);
    background: currentColor;
    transform: scale(1) translateY(-50%);
    transition: all 0.3s ease-in-out;
  }

  &--active {
    &::before, &::after {
      width: mini.rem(20);
      top: 50%;
      //left: calc(50% - mini.rem(10));
      //color: var(--accent-text-color);
      //transition: none;
      @include breakpoint.small-tablet {
        //left: calc(50% - mini.rem(7));
      }
    }
    &::before {
      left: mini.rem(-1);
      transform: rotate(45deg) translateY(calc(-50% + mini.rem(-1) / 2));
    }

    &::after {
      left: mini.rem(1);
      transform: rotate(-45deg) translateY(calc(-50% + mini.rem(-1) / 2));
    }

    .burger__line {
      transform: scale(0) translateY(-50%) translateX(0);
      color: var(--accent-text-color);
      transition: all 0.3s ease-in-out;
    }
  }
  @include breakpoint.small-tablet {
    //--burger-width: 62px;
    //--burger-height: 23px;
    //--burger-line-height: 4px;
  }
}
