@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.conditions {
  padding: mini.rem(100) 0;
  @include breakpoint.small-tablet {
    padding: mini.rem(80) 0;
  }
  @include breakpoint.mobile {
    padding: mini.rem(60) 0;
  }
  // .conditions__container
  &__container {
  }
  // .conditions__row
  &__row {
    display: flex;
    background: var(--bg-color);
    @include breakpoint.tablet {
      flex-direction: column;
      &:not(:last-child) {
        margin-bottom: mini.rem(30);
      }
    }
    @include breakpoint.mobile {
      &:not(:last-child) {
        margin-bottom: mini.rem(20);
      }
    }
    &:nth-child(even) {
      .conditions__content {
        order: -1;
        @include breakpoint.tablet {
          order: 1;
        }
      }
    }
    > * {
      width: 50%;
      max-width: 50%;
      @include breakpoint.tablet {
        width: 100%;
        max-width: 100%;
        max-height: mini.rem(570);
      }
    }
  }
  // .conditions__picture
  &__picture {
    width: 100%;
    picture {
      display: block;
      height: 100%;
    }
  }
  // .conditions__image
  &__image {
    width: 100%;
    height: 100%;
  }
  // .conditions__content
  &__content {
    padding: mini.rem(73) mini.rem(80) mini.rem(80);
    @include breakpoint.big-desktop {
      padding: mini.rem(50);
    }
    @include breakpoint.small-tablet {
      padding: mini.rem(30);
    }
    @include breakpoint.mobile {
      padding: mini.rem(20) mini.rem(20) mini.rem(30);
    }
  }
  // .conditions__title
  &__title {
    margin: 0;
    font-family: Didona, sans-serif;
    font-weight: 400;
    font-size: mini.rem(38);
    line-height: 126%;
    margin-bottom: mini.rem(28);
    max-width: mini.rem(680);
    @include breakpoint.small-tablet {
      font-size: mini.rem(30);
      margin-bottom: mini.rem(11);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(24);
    }
  }
  // .conditions__text
  &__text {
    color: rgba(0, 0, 0, 0.6);
    max-width: mini.rem(680);
    &:not(:last-child) {
      margin-bottom: mini.rem(15);
      @include breakpoint.small-tablet {
        margin-bottom: mini.rem(10);
      }
    }
  }
  // .conditions__subtitle
  &__subtitle {
    font-weight: 400;
    font-size: mini.rem(22);
    line-height: 127%;
    margin-bottom: mini.rem(34);
    max-width: mini.rem(630);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(11);
    }
    @include breakpoint.mobile {
      font-size: mini.rem(18);
    }
  }
  // .conditions__list
  &__list {
    max-width: mini.rem(680);
  }
  // .conditions__item
  &__item {
    position: relative;
    color: rgba(0, 0, 0, 0.6);
    padding-left: mini.rem(34);
    @include breakpoint.small-tablet {
      padding-left: mini.rem(29);
    }
    &::before {
      @include mini.pseudo;
      position: absolute;
      width: mini.rem(14);
      height: mini.rem(20);
      background-image: url(../img/favicon.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      left: 0;
      top: mini.rem(4);
    }
    &:not(:last-child) {
      margin-bottom: mini.rem(15);
      @include breakpoint.small-tablet {
        margin-bottom: mini.rem(10);
      }
    }
  }
}