@use "../mixins/mini";
@use "../mixins/breakpoint";

.news-details {
  margin-bottom: mini.rem(73);
  @include breakpoint.small-tablet {
    margin-bottom: mini.rem(61);
  }
  @include breakpoint.mobile {
    margin-bottom: mini.rem(59);
  }
  // .news-detailss__container
  &__container {
  }
  // .news-details__breadcrumbs
  &__breadcrumbs {
  }
  // .news-details__title
  &__title {
    @include breakpoint.mobile {
      font-size: mini.rem(24);
    }
  }
  // .news-details__swiper
  &__swiper {
    margin-bottom: mini.rem(34);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(24);
      margin-right: mini.rem(-20);
      margin-left: mini.rem(-20);
    }
  }
  // .news-details__image
  &__image {
    width: 100%;
  }
  // .news-details__content
  &__content {
    max-width: mini.rem(1266);
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-bottom: mini.rem(70);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(42);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(29);
    }
  }
  // .news-details__time
  &__time {
    font-family: "Bebas Neue Cyrillic", sans-serif;
    font-size: mini.rem(24);
    line-height: 117%;
    text-align: center;
    opacity: 0.3;
    padding-bottom: mini.rem(28);
    border-bottom: mini.rem(1) solid var(--second-color);
    margin-bottom: mini.rem(64);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(44);
    }
    @include breakpoint.mobile {
      padding-bottom: mini.rem(18);
      margin-bottom: mini.rem(31);
    }
  }
  // .news-details__text
  &__text {
    &:not(:last-child) {
      margin-bottom: mini.rem(20);
      @include breakpoint.mobile {
        margin-bottom: mini.rem(14);
      }
    }
  }
  // .news-details__browse
  &__browse {
  }
}