@use "../mixins/mini";
@use "../mixins/breakpoint";

.brands {
  // .brands__container
  &__container {
  }
  // .brands__breadcrumbs
  &__breadcrumbs {
  }
  // .brands__title
  &__title {
  }
  // .brands__list
  &__list {
    margin-bottom: mini.rem(50);
  }
  // .brands__item
  &__item {
    &:not(:last-child) {
      @include breakpoint.small-tablet {
        margin-bottom: mini.rem(30);
      }
      @include breakpoint.mobile {
        margin-bottom: mini.rem(20);
      }
    }

    &:nth-child(even) {
      .article-brands__head {
        order: 1;
        @include breakpoint.small-tablet {
          order: 0;
        }
      }
      .article-brands__logo {
        right: 100%;
        @media (max-width: 1700px) {
          right: 50%;
        }
      }
    }
  }
  // .brands__article
  &__article {
  }
  // .brands__pages
  &__pages {
  }
}

.article-brands {
  background: var(--bg-color);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, minmax(mini.rem(540), 1fr));
  @include breakpoint.small-tablet {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, minmax(mini.rem(400), 1fr));
  }
  @include breakpoint.mobile {
    grid-template-rows: repeat(1, minmax(mini.rem(195), 1fr));
  }
  // .article-brands__head
  &__head {
    position: relative;
  }
  &__content {
    align-self: center;
    justify-self: center;
    max-width: mini.rem(560);
    padding: mini.rem(50) mini.rem(60);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include breakpoint.small-tablet {
      padding: mini.rem(140) mini.rem(60) mini.rem(50);
    }
    @include breakpoint.mobile {
      padding: mini.rem(80) mini.rem(20) mini.rem(30);
    }
  }
  // .article-brands__image
  &__image, picture {
    width: 100%;
    height: 100%;
  }
  // .article-brands__logo
  &__logo {
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
    position: absolute;
    width: mini.rem(360);
    height: mini.rem(200);
    display: flex;
    align-self: center;
    justify-content: center;
    padding: mini.rem(36) mini.rem(50);
    background: rgba(255, 255, 255, 0.75);
    backdrop-filter: blur(15px);
    @media (any-hover: hover) {
      &:hover {
        opacity: 1;
      }
    }
    @media (max-width: 1700px) {
      right: 50%;
      transform: translate(50%, -50%);
    }
    @include breakpoint.small-tablet {
      top: 100%;
    }
    @include breakpoint.mobile {
      width: mini.rem(230);
      height: mini.rem(120);
      padding: mini.rem(20) mini.rem(35);
    }
    picture {
      display: flex;
      align-self: center;
      justify-content: center;
    }
    .logo__image {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  // .article-brands__title
  &__title {
  }
  &__text {
    margin-bottom: mini.rem(30);
    @include breakpoint.mobile {
      margin-bottom: mini.rem(20);
    }
  }
  .logo {
  }
  // .logo__image
  .logo__image {
  }
  // .article-brands__button
  &__button {
    min-width: mini.rem(160);
  }
}