@use '../mixins/mini';
@use '../mixins/breakpoint';

.news {
   // .news__container
   &__container {
   }
   // .news__breadcrumbs
   &__breadcrumbs {
   }
   // .news__title
   &__title {
   }
   // .news__list
   &__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: mini.rem(86) mini.rem(40);
      margin-bottom: mini.rem(56);
      @include breakpoint.tablet {
         gap: mini.rem(40) mini.rem(20);
         margin-bottom: mini.rem(50);
      }
      @include breakpoint.small-tablet {
         grid-template-columns: repeat(2, 1fr);
      }
      @include breakpoint.mobile {
         grid-template-columns: repeat(1, 1fr);
         gap: mini.rem(30);
         margin-bottom: mini.rem(46);
      }
   }
   // .news__item
   &__item {
   }
   // .news__article
   &__article {
   }
   &__pages {
   }
   &--other {
      margin-bottom: mini.rem(96);
      @include breakpoint.tablet {
         margin-bottom: mini.rem(91);
      }
      @include breakpoint.mobile {
         margin-bottom: mini.rem(74);
      }
      .news__list {
         margin-bottom: 0;
         @include breakpoint.tablet {
            display: block;
         }
         @include breakpoint.mobile {
            display: grid;
         }
      }
   }
}

.article-news {
   font-family: 'Bebas Neue Cyrillic', sans-serif;
   position: relative;
   height: 100%;
   &__wrap,
   &__play {
      width: auto;
      height: auto;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      position: relative;
      margin-bottom: mini.rem(30);
      padding-top: 65%;
      overflow: hidden;
      @include breakpoint.small-tablet {
         margin-bottom: mini.rem(26);
      }
   }
   .play__video {
      top: 0;
      position: absolute;
   }
   &__button-play {
      @include breakpoint.small-tablet {
         width: mini.rem(110);
         height: mini.rem(110);
         background-size: mini.rem(40) mini.rem(40);
      }
   }
   // .article-news__image
   &__image,
   picture {
      top: 0;
      position: absolute;
      width: 100%;
      height: 100%;
   }
   // .article-news__time
   &__time {
      opacity: 0.3;
      font-size: mini.rem(18);
      line-height: 111%;
      margin-bottom: mini.rem(12);
      padding: 0;
      @include breakpoint.desktop {
         padding: 0 mini.rem(30);
      }
      @include breakpoint.tablet {
         font-size: mini.rem(16);
         line-height: 125%;
         margin-bottom: mini.rem(5);
      }
   }
   // .article-news__title
   &__title {
      font-family: Didona, sans-serif;
      line-height: 127%;
      font-weight: 400;
      padding: 0;
      font-size: mini.rem(24);
      word-break: break-word;
      @include breakpoint.desktop {
         padding: 0 mini.rem(30);
         font-size: mini.rem(30);
      }
      @include breakpoint.mobile {
         font-size: mini.rem(20);
      }
   }
   &__label {
      opacity: 0;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: mini.rem(18);
      font-weight: 400;
      line-height: 111%;
      text-align: center;
      backdrop-filter: blur(mini.rem(15));
      background: rgba(255, 255, 255, 0.1);
      margin: mini.rem(30);
      padding: mini.rem(25) 0 mini.rem(20);
      width: calc(100% - mini.rem(30) * 2);
      color: var(--light-color);
      transition: opacity 0.3s ease 0s;
      @include breakpoint.tablet {
         width: calc(100% - mini.rem(20) * 2);
         margin: mini.rem(20);
      }
      &::after {
         @include mini.pseudo;
         width: mini.rem(12);
         height: mini.rem(12);
         background-image: url(../img/svg/arrow-btn-light.svg);
         background-repeat: no-repeat;
         background-size: contain;
         background-position: center;
         margin-left: mini.rem(10);
         margin-bottom: mini.rem(4);
         transition: width 0.3s ease 0s, margin-left 0.3s ease 0s;
      }
   }
   &__link {
      &::before {
         @include mini.pseudo;
         position: absolute;
         inset: 0;
         //background: red;
      }
   }
   @media (any-hover: hover) {
      &:hover {
         .article-news__label {
            opacity: 1;
         }
      }
   }
}
