@use "../mixins/mini";
@use "../mixins/breakpoint";

.search-details {
  // .search-details__container
  &__container {
  }

  // .search-details__breadcrumbs
  &__breadcrumbs {
  }

  // .search-details__title
  &__title {
  }

  // .search-details__list
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: mini.rem(48) mini.rem(40);
    margin-bottom: mini.rem(76);

    > * {
      justify-self: center;
    }

    @media (max-width: mini.rem(1500)) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include breakpoint.tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: mini.rem(48) mini.rem(20);
    }
    @include breakpoint.small-tablet {
      gap: mini.rem(42);
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: mini.rem(56);
    }
  }

  // .search-details__item
  &__item {
    @include breakpoint.small-tablet {
      max-width: mini.rem(420);
    }
  }

  // .search-details__card
  &__card {
  }

  .pages__button {
    display: flex;
  }
  .swiper-slide {
    width: 100% !important;
  }
}

