@use "../../mixins/mini";
@use "../../mixins/breakpoint";

.hero-partners {
  // .hero-partners__container
  &__container:not(.container) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  // .hero-partners__swiper
  &__swiper {
    .swiper-slide {
      &::after {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.2) 18.5%, rgba(0, 0, 0, 0.3) 49.5%, rgba(0, 0, 0, 0.25) 84%, rgba(0, 0, 0, 0.3) 100%), rgba(0, 0, 0, 0.2);
      }
    }
  }
  &__title {
    margin-bottom: mini.rem(22);
    @include breakpoint.small-tablet {
      margin-bottom: mini.rem(8);
    }
    @include breakpoint.mobile {
      margin-bottom: mini.rem(4);
    }
  }
  &__subtitle {
    text-align: center;
  }
  &__head {
    color: var(--light-color);
    position: absolute;
    z-index: 10;
    padding: 0 var(--container-offset);
  }
  .swiper-slide {
    //width: 50%;
  }
}